import {
  TransformComponent
} from '../../../../engine/components'
import { group } from './util'

function getEntity(world, id) {
  const entityId = group.get(world, id)

  if (!entityId) {
    return null
  }

  const entity = world.getEntity(entityId)

  if (!entity) {
    return null
  }

  return entity
}

export function shake(id, options) {
  let timer = 0
  options = options || {}

  function process(world) {
    const { amplitude = 0, duration = 0 } = options
    const entity = getEntity(world, id)

    if (entity === null) {
      timer = 0
      return true
    } else {
      const Transform = entity.getComponent(TransformComponent)
      if (duration <= timer) {
        Transform.x = 0
        Transform.y = 0
        timer = 0
        return true
      }
      Transform.x = Math.round(Math.random() * amplitude * 2) - amplitude
      Transform.y = Math.round(Math.random() * amplitude * 2) - amplitude
      timer++
      return false
    }
  }

  return {
    process
  }
}
