import React from 'react'
import ReactModal from 'react-modal'

function SaveDialog(props) {
  const { isOpen, onClose, status } = props

  function closeHandle(e) {
    onClose && onClose(e)
  }

  return (
    <ReactModal
      isOpen={isOpen}
      closeTimeoutMS={200}
      ariaHideApp={false}
      contentLabel="save dialog"
      style={{
        overlay: {
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          zIndex: 6,
          backgroundColor: null
        }
      }}
      className="dialog"
    >
      <div className="dialog-container">
        <div className="dialog-content">
          <div className="dialog-icon">
            {
              status === 'saving'
                ? (
                  <div className="spinner-box">
                    <div className="circle-border">
                      <div className="circle-core"></div>
                    </div>
                  </div>
                )
                : (
                  <div className="sa-success">
                    <div className="sa-success-tip"></div>
                    <div className="sa-success-long"></div>
                    <div className="sa-success-placeholder"></div>
                    <div className="sa-success-fix"></div>
                  </div>
                )
            }
          </div>
          <div className="dialog-text">
            { status === 'saving' ? 'Saving...' : 'Saved.' }
          </div>
        </div>
        <div className="dialog-interaction">
          <div
            className="dialog-button"
            style={{
              margin: status === 'saving' ? -55 : 0
            }}
          >
            <button onClick={closeHandle}>OK</button>
          </div>
        </div>
      </div>
    </ReactModal>
  )
}

export default SaveDialog
