import React from 'react'
import ReactModal from 'react-modal'

function WarningDialog(props) {
  const { isOpen, onConfirm, onClose } = props

  function yesHandle(e) {
    onConfirm && onConfirm(e)
  }

  function noHandle(e) {
    onClose && onClose(e)
  }

  return (
    <ReactModal
      isOpen={isOpen}
      closeTimeoutMS={200}
      ariaHideApp={false}
      contentLabel="warning dialog"
      style={{
        overlay: {
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          zIndex: 6,
          backgroundColor: null
        }
      }}
      className="dialog"
    >
      <div className="dialog-container">
        <div className="dialog-content">
          <div className="dialog-icon">
            <div className="sa-warning">
              <span className="sa-warning-body"></span>
              <span className="sa-warning-dot"></span>
            </div>
          </div>
          <div className="dialog-text" style={{
            lineHeight: 1.2,
            textAlign: 'left',
            paddingTop: 5,
            paddingLeft: 80
          }}>
            未保存的数据将会丢失，是否继续？
          </div>
        </div>
        <div className="dialog-interaction">
          <div
            className="dialog-button"
            style={{
              margin: 0
            }}
          >
            <button className="dialog-button-yes" onClick={yesHandle}>Yes</button>
            <button className="dialog-button-no" onClick={noHandle}>No</button>
          </div>
        </div>
      </div>
    </ReactModal>
  )
}

export default WarningDialog
