import {
  TextComponent,
  ButtonComponent,
  TransformComponent
} from '../../../../engine/components'
import { ChoiceComponent } from '../../../components/tag'
import { group } from './util'

function createButton(world, textureId, x = 0, y = 0) {
  const entity = world.createEntity()
    .addComponent(ChoiceComponent)
    .addComponent(TransformComponent, { x, y })
    .addComponent(ButtonComponent, { useAnimation: false })
    .addComponent(TextComponent, {
      layer: 'UserLayer8',
      text: '',
      bgTextureId: textureId
    })
  
  return entity
}

export function button(list) {
  let gotoResult = {}
  if (!Array.isArray(list)) {
    list = [list]
  }

  function init(world) {
    list.forEach(item => {
      const { id, resource, options } = item
      const entityId = group.get(world, id)

      if (entityId) {
        return
      }
  
      const { x, y } = options
      const entity = createButton(world, resource, x, y)
      group.set(world, id, entity.id)
    })
  }

  function process(world) {
    const count = list.length

    for (let i = 0; i < count; i++) {
      const { id, options } = list[i]
      const entityId = group.get(world, id)

      if (!entityId) {
        continue
      }

      const entity = world.getEntity(entityId)
      const btn = entity.getComponent(ButtonComponent)
      if (btn.isClick === true) {
        const { action } = options
        gotoResult = {}
        if (typeof action === 'number') {
          gotoResult.node = action
        } else if (typeof action === 'function') {
          gotoResult = action(world.storyData, world.systemData.nodeIndex)
        } else {
          gotoResult = null
        }

        list.forEach(item => {
          const entityId = group.get(world, item.id)
          const entity = world.getEntity(entityId)
          world.removeEntity(entity)
          group.delete(world, item.id)
        })
        return true
      }
    }

    return false
  }

  function goto() {
    return gotoResult
  }

  return {
    init,
    process,
    goto
  }
}

