import { group } from './util'

export function hide() {
  function process(world) {
    let entityId = group.get(world, 'text')
    if (entityId) {
      const dialog = world.getEntity(entityId)
      group.delete(world, 'text')
      world.removeEntity(dialog)
    }

    entityId = group.get(world, 'name')
    if (entityId) {
      const name = world.getEntity(entityId)
      group.delete(world, 'name')
      world.removeEntity(name)
    }

    return true
  }

  return { process }
}
