import * as PIXI from 'pixi.js'
import sound from 'pixi-sound'
import { saveToLocalStorage, loadFromLocalStorage } from '../utils'

type PlayOptions = {
  start?: number
  end?: number
  speed?: number
  loop?: boolean
  volume?: number
  sprite?: string
  muted?: boolean
  complete?: any
  loaded?: any
}

export class Sound {
  static muted = false

  static set volume(value: number) {
    sound.volumeAll = value
  }

  static get volume() {
    return sound.volumeAll
  }

  static play(alias: string, options?: PlayOptions) {
    sound.play(alias, options)
  }

  static stop(alias: string) {
    sound.stop(alias)
  }

  static stopAll() {
    sound.stopAll()
  }

  static mute() {
    this.muted = true
    sound.muteAll()
  }

  static unmute() {
    this.muted = false
    sound.unmuteAll()
  }

  static saveState() {
    const state = {
      muted: this.muted,
      volume: this.volume
    }
    saveToLocalStorage('sound', JSON.stringify(state))
  }

  static loadState() {
    const state = JSON.parse(loadFromLocalStorage('sound', true))
    if (state) {
      this.muted = state.muted
      this.volume = state.volume

      if (this.muted === true) {
        this.mute()
      }
    } else {
      this.volume = 0.8
    }
  }

  private sound: PIXI.sound.Sound

  constructor(public alias: string) {
    this.sound = sound.find(alias)
  }

  get isPlaying() {
    return this.sound.isPlaying
  }

  public play(options?: PlayOptions) {
    this.sound.play(options)
  }

  public stop() {
    this.sound.stop()
  }
}
