import { ISystem } from '../core'
import { Entity } from '../core/entity'
import { TextComponent, ButtonComponent, TransformComponent } from '../components'

export class TextButtonSystem extends ISystem {
  public execute(delta: number) {
    const entities = this.queries.entities.results

    for (const entity of entities) {
      const text = entity.getComponent(TextComponent)
      const button = entity.getComponent(ButtonComponent)

      if (button.isBind === false) {
        button.isBind = true
        text.view.interactive = true
        text.view.buttonMode = true
        text.view
          .on('pointerdown', this.processButtonDown(entity))
          .on('pointerup', this.processButtonUp(entity))
          .on('pointerupoutside', this.processButtonUp(entity))
          .on('pointertap', () => {
            button.clickEvent = true
            button.deltaTimer = 0
          })
        button.origin = text.view
      }

      button.isClick = false
      if (button.clickEvent === true) {
        if (button.deltaTimer >= 10) {
          button.isClick = true
          button.clickEvent = false
          button.deltaTimer = 0
        } else {
          button.deltaTimer += delta
        }
      }
    }
  }

  private processButtonUp(entity: Entity) {
    const text = entity.getComponent(TextComponent)
    const button = entity.getComponent(ButtonComponent)
    const view = text.view

    return () => {
      if (button.useAnimation === true) {
        if (entity.hasComponent(TransformComponent)) {
          entity.getComponent(TransformComponent).scale = 1
        } else {
          view.scale.set(1)
        }
        text.anchor = { x: 0, y: 0 }
      }

      this.onButtonUp(text)
    }
  }

  private processButtonDown(entity: Entity) {
    const text = entity.getComponent(TextComponent)
    const button = entity.getComponent(ButtonComponent)
    const view = text.view

    return () => {
      if (button.useAnimation === true) {
        if (entity.hasComponent(TransformComponent)) {
          entity.getComponent(TransformComponent).scale = 0.95
        } else {
          view.scale.set(0.95)
        }
        // 0.025 = (1 - 0.95) / 2
        text.anchor = { x: -0.025, y: -0.025 }
      }

      this.onButtonDown(text, button)
    }
  }

  private onButtonUp(textC: TextComponent) {
    textC.btnView.texture = textC.bgTexture
    Object.assign(textC.textView.style, textC.style)
  }

  private onButtonDown(textC: TextComponent, btnC: ButtonComponent) {
    if (btnC.texture) {
      textC.btnView.texture = btnC.texture
    }
    Object.assign(textC.textView.style, btnC.style)
  }
}

TextButtonSystem.queries = {
  entities: [TextComponent, ButtonComponent]
}
