import { Sprite, Texture } from 'pixi.js'

class Flower extends Sprite {
  constructor(texture, clientWidth, clientHeight, maxDist) {
    super(texture)
    this.x = Math.random() * (clientWidth + maxDist) - maxDist / 2
    this.y = Math.random() * (clientHeight + maxDist) - maxDist / 2
    this.z = Math.random() * 0.5 + 0.3
    this.vx = (Math.random() * 1 + 1) * this.z
    this.vy = (Math.random() * 0.5 + 1) * this.z
    this.rotation = Math.random() * Math.PI * 2
    this.anchor.set(0.5)
    this.spin = this.rotation > Math.PI ? 1 : -1
  }
}

export default class FlowerFlyBackground {
  constructor({ width, height, amount, maxDist, parent, textureId }) {
    this.width = width
    this.height = height
    this.amount = amount
    this.maxDist = maxDist
    this.parent = parent
    this.textureId = textureId
    this.flowerArr = []

    this.init()
  }

  init() {
    const { width, height, amount, maxDist, parent } = this

    for (let i = 0; i < amount; i++) {
      const texture = Texture.from(`title_gold-${this.textureId}`)
      const flower = new Flower(texture, width, height, maxDist)
      this.flowerArr.push(flower)
      parent.addChild(flower)
    }
  }

  update(obj) {
    const { width, height, maxDist } = this
    obj.rotation += (0.003 * obj.spin)
    obj.x += obj.vx
    obj.y += obj.vy
    if (obj.x > width + maxDist / 2) {
      obj.x = -(maxDist / 2)
    } else if (obj.xpos < -(maxDist / 2)) {
      obj.x = width + maxDist / 2
    }
    if (obj.y > height + maxDist / 2) {
      obj.y = -(maxDist / 2)
    } else if (obj.y < -(maxDist / 2)) {
      obj.y = height + maxDist / 2
    }
  }

  tick() {
    const { flowerArr } = this

    flowerArr.forEach(obj => {
      this.update(obj)
    })
  }
}
