import { Sprite, Texture, Text, Container } from 'pixi.js'
import RenderComponent from './base/render'
import { componentRegister } from '../core'
import RainbowText from '../lib/rainbow-text'

type AxisPosition = {
  x: 'left' | 'center' | 'right'
  y: 'top' | 'center' | 'bottom'
}

type TextStyle = {
  fontFamily?: string | string[],
  fontWeight?: string,
  fontSize?: number,
  fill?: string,
  lineJoin?: 'round',
  stroke?: string,
  strokeThickness?: number,
  wordWrap?: boolean,
  wordWrapWidth?: number
}

export class TextComponent extends RenderComponent {
  // serialize fields
  public text: string
  public style: TextStyle
  public position: AxisPosition
  public bgTextureId: string
  public padding: number
  // temp fields
  public bgTexture: Texture
  public textView: Text
  public btnView: Sprite

  constructor() {
    super()
    this.text = ''
    this.style = {}
    this.position = {
      x: 'left',
      y: 'top'
    }
    this.bgTextureId = null
    this.padding = 0

    this.bgTexture = null
    this.textView = null
    this.btnView = null
  }

  public init() {
    this.view = new Container()
    if (this.bgTextureId) {
      this.bgTexture = Texture.from(this.bgTextureId)
    } else {
      this.bgTexture = Texture.EMPTY
    }
    this.btnView = new Sprite(this.bgTexture)
    if (this.bgTextureId) {
      this.style = {
        wordWrap: true,
        wordWrapWidth: this.btnView.width - this.padding * 2,
        ...this.style
      }
    }
    this.textView = new RainbowText(this.text, this.style)
    this.textView.resolution = 2
    this.justifyContent()
    this.view.addChild(this.btnView, this.textView)
  }

  public justifyContent() {
    const { position } = this
    const { width: btnW, height: btnH } = this.btnView
    const { width: textW, height: textH } = this.textView

    if (position.x === 'left') {
      this.textView.x = this.padding
    } else if (position.x === 'center') {
      this.textView.x = Math.abs(btnW - textW) / 2
    } else if (position.x === 'right') {
      this.textView.x = Math.abs(btnW - textW) - this.padding
    }
    if (position.y === 'top') {
      this.textView.y = this.padding
    } else if (position.y === 'center') {
      this.textView.y = Math.abs(btnH - textH) / 2
    } else if (position.y === 'bottom') {
      this.textView.y = Math.abs(btnH - textH) - this.padding
    }
  }

  public clear() {
    this.revert()
    this.text = ''
    this.style = {}
    this.position = {
      x: 'left',
      y: 'top'
    }
    this.bgTextureId = null
    this.padding = 0

    this.textView?.destroy()
    this.textView = null
    this.btnView?.destroy()
    this.btnView = null
    this.bgTexture = null
  }

  public toJSON() {
    const { text, style, position, bgTextureId, padding } = this
    const render = this.getRenderJSON()

    return { text, style, position, bgTextureId, padding,
      ...render
    }
  }
}

componentRegister('TextComponent')(TextComponent)
