import { scene, dialog, character, choice, flow, util } from '../../api'
import { color } from '../../../config'

function commomOptions(next1 = 0, next2 = 0, next3 = 0) {
  return [
    choice.select(
      [
        '攻击',
        '防御',
        '逃跑'
      ],
      [
        (data, index) => {
          return {
            node: index + 1 + next1
          }
        },
        (data, index) => {
          return {
            node: index + 1 + next2
          }
        },
        (data, index) => {
          return {
            node: index + 1 + next3
          }
        }
      ]
    )
  ]
}

