import { IComponent, ISystem } from '../core'
import {
  TransformComponent,
  TransparentComponent,
  DisplacementComponent,
  FadeComponent
} from '../components'

export class TransformSystem extends ISystem {
  public execute(delta: number) {
    const disp = this.queries.disp.results

    for (const entity of disp) {
      const Transform = entity.getComponent(TransformComponent)
      const Displacement = entity.getComponent(DisplacementComponent)
      const result = this.transform(Transform, Displacement, delta)
      if (result === true) {
        entity.removeComponent(DisplacementComponent)
      }
    }

    const fade = this.queries.fade.results

    for (const entity of fade) {
      const Transparent = entity.getComponent(TransparentComponent)
      const Fade = entity.getComponent(FadeComponent)
      const result = this.transform(Transparent, Fade, delta)
      if (result === true) {
        entity.removeComponent(FadeComponent)
      }
    }
  }

  public transform(
    target: IComponent,
    method: IComponent,
    delta: number
  ): boolean {
    const component = target as any
    const { start, end, duration } = method as any

    for (const key in end) {
      const offset = end[key] - start[key]
      if (component[key] !== end[key]) {
        component[key] += (offset / duration * delta)
        if (offset >= 0) {
          if (component[key] >= end[key]) {
            component[key] = end[key]
            return true
          }
        } else {
          if (component[key] <= end[key]) {
            component[key] = end[key]
            return true
          }
        }
      }
    }
    return false
  }
}

TransformSystem.queries = {
  disp: [TransformComponent, DisplacementComponent],
  fade: [TransparentComponent, FadeComponent]
}
