import { scene, choice, dialog } from './api'

// 海风镇大街
export const townStreet = [
  scene.show('da_jie', 'map01010'),
  scene.show('lu_pai', 'map01010a', {
    render: {
      position: {
        x: 0,
        y: 48
      }
    }
  }),
]

// 房间内事件
export function roomEvent(length1 = 0, length2 = 0) {
  return [
    choice.button(
      {
        id: 'door',
        resource: 'map01011-0',
        options: {
          x: 188,
          y: 107
        }
      },
    ),
  
    dialog.show('接下来想前往什么地方呢？'),
    choice.select(
      [
        '民宿大厅',
        '海风镇大街',
        '取消'
      ],
      [
        (data, index) => {
          return {
            node: index + 1 + length1
          }
        },
        (data, index) => {
          return {
            node: index + 1 + length2
          }
        },
        data => {
          return {
            node: data.room
          }
        }
      ]
    ),
  ]
}