import React from 'react'
import './index.css'

import Item from './item'
import HexagonButton from '../hexagon-button'
import Switch from '../switch'
import Volume from './volume'

import { Sound } from '../../engine/sound'

class Config extends React.PureComponent {
  constructor() {
    super()
    this.data = React.createRef()
    this.state = {
      checked: false
    }
  }

  componentDidMount() {
    this.setState({
      checked: !Sound.muted,
      volume: Sound.volume
    })
  }

  switchHandle = checked => {
    if (checked) {
      Sound.unmute()
    } else {
      Sound.mute()
    }
    Sound.saveState()
    this.setState({
      checked
    })
  }

  volumeHandle = value => {
    Sound.volume = value / 100
    Sound.saveState()
  }

  exportHandle = () => {
    const textarea = this.data.current
    const data = this.props.getArchive() || ''
    textarea.value = data
  }

  selectAllHandle = () => {
    const textarea = this.data.current
    textarea.select()
  }

  render() {
    return (
      <div className="game-config">
        <section>
          <h3 className="game-config-title">系统</h3>
          <Item name="声音"><Switch checked={this.state.checked} onChange={this.switchHandle} /></Item>
          <Item name="音量"><Volume value={Sound.volume * 100} onChange={this.volumeHandle} /></Item>
          <HexagonButton onClick={this.props.onPanel}>on/off<br />状态面板</HexagonButton>
          {
            this.props.show === 3 && (
              <>
                <HexagonButton onClick={this.props.onSave}>存储进度</HexagonButton>
                <HexagonButton onClick={this.props.onLoad}>读取进度</HexagonButton>
                <HexagonButton onClick={this.props.onBack}>返回标题</HexagonButton>
              </>
            )
          }
        </section>
        <section>
          <h3 className="game-config-title">数据</h3>
          <textarea className="game-input-data" ref={this.data}></textarea>
          <div className="game-data-options">
            <button onClick={this.exportHandle}>导出</button>
            <button onClick={this.selectAllHandle}>全选</button>
            <button>导入</button>
          </div>
        </section>
      </div>
    )
  }
}

export default Config
