import { bgm, stage, scene, dialog, character, flow, util } from '../../api'
import { color } from '../../../config'

const commonStart1 = [
  stage.curtain.down(),
  scene.show('voiceover', 'voiceover'),
  stage.curtain.up(500),
]
const commonStart2 = [
  stage.voiceover.show(
      util.colorText('海神寺', color.deepskyblue)
  ),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.hide('voiceover'),
  scene.show('temple', 'map01004'),
  stage.curtain.up(500),

  bgm.play('shopping', {
    loop: true
  }),
]

const commonEnd = [
  bgm.stop('shopping'),
  stage.curtain.down(500),
  flow.goto('chapter1_5')
]

// 海神寺
export const chapter1_5_3 = [
  ...commonStart1,
  
  // NOTE: 原游戏这里没有提示章节标题
  stage.voiceover.show(
    util.colorText('第二章    《失落》', color.burlywood),
  ),

  ...commonStart2,

  dialog.show(
    '哇！这就是海神寺嘛？感觉真不错呢……！',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      [
        '那是一间不算大的寺庙，斑驳的墙上可以看得出日晒雨淋的陈旧，耸立在门前的牌坊，彰显着岁月的痕迹。',
        '寺庙的另一头，有一处观景台，旁边耸立着一块木牌，上头用潦草飞舞的毛笔字迹记载着海神寺的过去。',
        '踏上十几阶阶梯来到观景台，放眼望去，在不远处的海平面上耸立着一座小岛，相传是跟海神寺本身的传说有关。',
        '我有些眼花缭乱的四处逛着，络绎不绝的游客，在寺庙中穿梭着，不时可以看到虔诚祈祷的人们，其中绝大多数都是成双成对的情侣。',
        '正当我沉浸在海神寺庄严肃穆的气氛时……'
      ],
      color.lightskyblue
    )
  ),
  dialog.show('碰！'),

  scene.shake('temple', scene.baseShake),

  dialog.show(
    '哎哟~！',
    '以翔'
  ),

  character.show('xing_sha_1'),

  dialog.show(
    '唔。',
    '？？？'
  ),

  dialog.show(
    util.colorText(
      [
        '一个不留神，我撞到了身旁的女子。一时的慌乱，让我冷不防地踩在她脚上。',
        '我这一踩，也让她不由得发出轻轻的一声「哎哟」。'
      ],
      color.lightskyblue
    )
  ),
  dialog.show(
    '对……对不起！不小心撞到你，还踩到你的脚！你还好吧？',
    '以翔'
  ),
  dialog.show(
    '嗯……',
    '？？？'
  ),
  dialog.show(
    '真的是很抱歉！',
    '以翔'
  ),
  dialog.show(
    '不……不会……我没事……',
    '？？？'
  ),
  dialog.show(
    '嗯……',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      [
        '转身离去的是一位有着柔顺长发的女子。',
        '女子的神情显得很哀伤，彷佛肩负着沉重的重担……',
        '不知怎么的，她看起来很……'
      ],
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '（是我撞痛她的关系吗……）',
      '（总觉得她……她好像在哪看过……）'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '看着她离去的背影，我纳闷地想着。',
      color.lightskyblue
    )
  ),

  ...commonEnd
]

export const chapter1_5_3end = [
  ...commonStart1,
  ...commonStart2,

  dialog.show(
    util.colorText(
      '我有些眼花缭乱的四处逛着，络绎不绝的游客，在寺庙中穿梭着，不时可以看到虔诚祈祷的人们，其中绝大多数都是成双成对的情侣。',
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '到处都是情侣啊……',
      '要是绫音现在也在着就好了。',
      '唉~还是回民宿去吧。'
    ],
    '以翔'
  ),

  ...commonEnd
]