/**
 * 初始化路标数据
 * @param {string} chapter
 * @param {number} [index] 
 */
export function initRoadSignData(chapter, index = 0) {
  return {
    /**
     * 记录时的章节
     * @type {string}
     */
    chapter,

    /**
     * 记录时的下标
     * @type {number}
     */
    index,

    /**
     * 要跳转到的节点下标
     * @type {number}
     */
    target: 0,

    /**
     * 进入海边的次数
     * @type {number}
     */
    seaside: 0,

    /**
     * 进入商店街的次数
     * @type {number}
     */
    shopping: 0,

    /**
     * 进入海神寺的次数
     * @type {number}
     */
    temple: 0,

    /**
     * 进入帆船公园的次数
     * @type {number}
     */
    park: 0,

    /**
     * 进入彩虹天堂的次数
     * @type {number}
     */
    hostel: 0
  }
}
