import React from 'react'
import './index.css'

import Loader from '../loader'
import MenuIcon from '../menu-icon'
import MenuLayout from '../menu-layout'
import Config from '../config'
import About from '../about'

import SaveDialog from '../dialog/save'
import WarningDialog from '../dialog/warning'
import '../dialog/index.css'

import game from '../../game'
import { TitleScene, MainScene } from '../../game/scene'

class LoveMemory extends React.Component {
  constructor() {
    super()
    this.loadError = false
    this.loader = React.createRef()
    this.canvas = React.createRef()
    this.state = {
      show: 0, // 1 | 3: Config menu, 2: About menu
      menu: false, // is show menu switch
      loader: {
        loading: true,
        progress: 0,
        error: null
      },
      width: 0,
      height: 0,
      dialog: 0, // 1: save dialog, 2: warning dialog
      dialogStatus: ''
    }
  }

  componentDidMount() {
    this.resize()
    this.addEvents()
    game.load().then(
      () => {
        // 让 loading 动画缓缓先
        setTimeout(() => {
          this.setState({
            loader: {
              ...this.state.loader,
              loading: false
            }
          }, () => {
            // show
            game.start(TitleScene)
            game.renderer.view.style.opacity = 1
          })
        }, 1000)
      },
      error => {
        console.warn(error)
        this.loadError = true
        this.loader.current.detailsHandle()
        this.setState({
          loader: {
            ...this.state.loader,
            error
          }
        })
      }
    )
  }

  componentWillUnmount() {
    this.removeEvents()
  }

  addEvents() {
    this.canvas.current.appendChild(game.renderer.view)

    window.addEventListener('resize', this.resize)

    game.loader.onProgressed.add(this.progressHandle)

    TitleScene.onStartClicked.add(this.clickStartHandle)
    TitleScene.onLoadClicked.add(this.clickLoadHandle)
    TitleScene.onSettingClicked.add(this.clickSettingHandle)
    TitleScene.onAboutClicked.add(this.clickAboutHandle)
  }

  removeEvents() {
    this.canvas.current.removeChild(game.renderer.view)

    window.removeEventListener('resize', this.resize)

    game.loader.onProgressed.remove(this.progressHandle)

    TitleScene.onStartClicked.remove(this.clickStartHandle)
    TitleScene.onLoadClicked.remove(this.clickLoadHandle)
    TitleScene.onSettingClicked.remove(this.clickSettingHandle)
    TitleScene.onAboutClicked.remove(this.clickAboutHandle)
  }

  resize = () => {
    game.resize()
    this.setState({
      width: game.renderer.view.style.width,
      height: game.renderer.view.style.height
    })
  }

  progressHandle = (progress) => {
    if (this.loadError) {
      return
    }
    progress = Math.ceil(progress)
    const prevProgress = this.state.loader.progress
    const offset = progress - prevProgress

    if (offset >= 20 || progress >= 100) {
      this.setState({
        loader: {
          ...this.state.loader,
          progress
        }
      })
    }
  }

  clickStartHandle = () => {
    game.to(MainScene).then(() => {
      this.setState({
        menu: true
      })
    })
  }

  clickLoadHandle = () => {
    game
      .to(MainScene, scene => {
        scene.loadArchive()
      })
      .then(() => {
        this.setState({
          menu: true
        })
      })
  }

  clickSettingHandle = () => {
    this.setState({
      show: 1
    })
  }

  clickAboutHandle = () => {
    this.setState({
      show: 2
    })
  }

  openMenuHandle = () => {
    this.setState({
      show: 3
    })
  }

  closeMenuHandle = () => {
    this.setState({
      show: 0
    })
  }

  togglePanelHandle = () => {
    const added = game.stats.added
    if (added) {
      game.removeStats()
    } else {
      game.addStats()
    }
  }

  closeDialogHandle = () => {
    this.setState({
      dialog: 0,
      dialogStatus: ''
    })
  }

  openSaveDialogHandle = () => {
    this.setState({
      dialog: 1,
      dialogStatus: 'saving'
    })
    this.saveArchive()
  }

  openLoadDialogHandle = () => {
    this.setState({
      dialog: 2,
      dialogStatus: 'load'
    })
  }

  openBackTitleDialogHandle = () => {
    this.setState({
      dialog: 2,
      dialogStatus: 'back'
    })
  }

  confirmWarnHandle = () => {
    const { dialogStatus } = this.state

    if (dialogStatus === 'back') {
      this.backTitle()
    } else if (dialogStatus === 'load') {
      this.loadArchive()
    }
  }

  saveArchive() {
    game.saveArchive().then(() => {
      setTimeout(() => {
        this.setState({
          dialogStatus: 'saved'
        })
      }, 500)
    })
  }

  loadArchive() {
    game.loadArchive().then(() => {
      this.closeDialogHandle()
      // 因为 dialog 有个 200ms 的动画，所以这里延迟 200ms
      setTimeout(() => {
        this.setState({
          show: 0
        })
      }, 200)
    })
  }

  getArchive = () => {
  }

  backTitle() {
    game.to(TitleScene).then(() => {
      this.closeDialogHandle()
      // 因为 dialog 有个 200ms 的动画，所以这里延迟 200ms
      setTimeout(() => {
        this.setState({
          show: 0,
          menu: false
        })
      }, 200)
    })
  }

  render() {
    const { show, menu, width, height, loader } = this.state
    const style = {
      width,
      height
    }

    if (show > 0) {
      game.pause()
    } else if (game.isPlay === false) {
      game.resume()
    }

    return (
      <>
        <div className="love-memory" style={style}>
          <Loader ref={this.loader} loader={loader} />
          {menu && (
            <div className="menu-icon-wrapper">
              <MenuIcon onClick={this.openMenuHandle} />
            </div>
          )}
          <MenuLayout show={show} onClose={this.closeMenuHandle}>
            {(show === 1 || show === 3) && (
              <Config
                show={show}
                onPanel={this.togglePanelHandle}
                onSave={this.openSaveDialogHandle}
                onLoad={this.openLoadDialogHandle}
                onBack={this.openBackTitleDialogHandle}
                getArchive={this.getArchive}
              />
            )}
            {show === 2 && <About />}
          </MenuLayout>
          <div ref={this.canvas}></div>
        </div>
        <SaveDialog
          isOpen={this.state.dialog === 1}
          status={this.state.dialogStatus}
          onClose={this.closeDialogHandle}
        />
        <WarningDialog
          isOpen={this.state.dialog === 2}
          onConfirm={this.confirmWarnHandle}
          onClose={this.closeDialogHandle}
        />
      </>
    )
  }
}

export default LoveMemory
