import { bgm, stage, scene, dialog, flow, util } from '../../api'
import { color } from '../../../config'

const commonStart = [
  stage.curtain.down(),
  scene.show('voiceover', 'voiceover'),
  stage.curtain.up(500),

  stage.voiceover.show(
    util.colorText('彩虹天堂', color.deepskyblue)
  ),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.hide('voiceover'),
  scene.show('hostel', 'map01003'),
  stage.curtain.up(500),

  bgm.play('nerves', {
    loop: true
  }),
]

const commonEnd = [
  bgm.stop('nerves'),
  stage.curtain.down(500),
  flow.goto('chapter2_4')
]

// 彩虹天堂
export const chapter2_4_5 = [
  ...commonStart,

  dialog.show(
    '啊！糟了！要是被他知道我住在这边还得了，肯定会来大闹特闹得，还是逃去别的地方吧！',
    '以翔'
  ),

  ...commonEnd
]
