import {
  TextComponent,
  TypewriterComponent
} from '../../../../engine/components'
import { InputComponent } from '../../../components/singleton'
import { voiceoverStyle } from '../../../config'
import { group } from './util'

const typewriterOptions = {
  speed: 22
}

function createVoiceover(world, text) {
  const entityId = group.get(world, 'voiceover')
  if (entityId) {
    changeVoiceover(world.getEntity(entityId), text)
    return
  }

  const entity = world.createEntity('voiceover')
    .addComponent(TextComponent, {
      layer: 'UserLayer9',
      style: voiceoverStyle,
      bgTextureId: 'voiceover',
      padding: 22,
      position: {
        x: 'left',
        y: 'center'
      }
    })
    .addComponent(TypewriterComponent, { text, ...typewriterOptions })
  
  group.set(world, 'voiceover', entity.id)
}

function changeVoiceover(entity, text) {
  const Text = entity.getComponent(TextComponent)
  Text.text = ''

  if (entity.hasComponent(TypewriterComponent)) {
    const Typewriter = entity.getComponent(TypewriterComponent)
    Typewriter.text = text
  } else {
    entity.addComponent(TypewriterComponent, { text, ...typewriterOptions })
  }
}

export function show(texts) {
  function init(world) {
    let text

    if (Array.isArray(texts)) {
      world.systemData.subIndex = 0
      text = texts[0]
      world.systemData.subIndex++
    } else {
      text = texts
    }
    createVoiceover(world, text)
  }

  function process(world) {
    const entityId = group.get(world, 'voiceover')
    const voiceover = world.getEntity(entityId)
    const input = world.singleton.getComponent(InputComponent)

    if (input.isClick === false) {
      return false
    }

    if (voiceover.hasComponent(TypewriterComponent)) {
      const text = voiceover.getComponent(TypewriterComponent).text
      voiceover.getComponent(TextComponent).text = text
      return false
    }

    if (
      Array.isArray(texts) &&
      world.systemData.subIndex < texts.length
    ) {
      const text = texts[world.systemData.subIndex]
      world.systemData.subIndex++
      changeVoiceover(voiceover, text)
      return false
    } else {
      return true
    }
  }

  return {
    init,
    process
  }
}

export function hide() {
  function process(world) {
    const entityId = group.get(world, 'voiceover')
    if (entityId) {
      const voiceover = world.getEntity(entityId)
      group.delete(world, 'voiceover')
      world.removeEntity(voiceover)
    }

    return true
  }

  return { process }
}
