import { IComponent, componentRegister } from '../../../engine/core'

export class StoryComponent extends IComponent {
  constructor() {
    super()
    this.entities = {}
    this.curChapter = null
    this.initialed = false
    this.nodeIndex = 0
    this.subIndex = 0
  }

  clear() {
    this.entities = {}
    this.curChapter = null
    this.initialed = false
    this.nodeIndex = 0
    this.subIndex = 0
  }

  toJSON() {
    const { entities, curChapter, initialed, nodeIndex, subIndex } = this
    return { entities, curChapter, initialed, nodeIndex, subIndex }
  }
}

componentRegister('StoryComponent')(StoryComponent)
