import { createComponent, addFade, fadeCompleted, group } from './util'
import * as character from '../../../config/character'

/**
 * 创建角色
 * @param {*} world engin world
 * @param {string} id 角色识别id
 * @param {string | any[]} key config 中预设的角色 key，或角色组件数组
 */
function createChar(world, id, key) {
  let char
  if (Array.isArray(key)) {
    char = key
  } else {
    char = character[key]
  }

  if (typeof char === 'undefined') {
    return
  }

  if (typeof group.get(world, id) !== 'undefined') {
    return
  }

  const entityIdList = []
  for (const comp of char) {
    const entity = createComponent(world, comp)
    addFade(entity, 1)
    entityIdList.push(entity.id)
  }
  group.set(world, id, entityIdList)
}

export function show(id, key = id) {
  function init(world) {
    createChar(world, id, key)
  }

  function process(world) {
    const entityIdList = group.get(world, id)
    let flag = true

    if (typeof entityIdList !== 'undefined') {
      entityIdList.forEach(item => {
        const entity = world.getEntity(item)
        if (!fadeCompleted(entity)) {
          flag = false
        }
      })
    }

    return flag
  }

  return {
    init,
    process
  }
}