import { IComponentClass, IComponent } from './component'
import { ObjectPool } from './object-pool'
import { generateUUID } from './utils'
import { World } from './world'

export type EntityClass = {
  onComponentAdded(): void
  onComponentRemoved(): void
}

export class Entity {
  public static _pool: ObjectPool<Entity> = ObjectPool.of(Entity)
  public id: string = generateUUID()
  public alias?: string
  public isSingleton: boolean = false
  public _world?: World
  private _components: Map<IComponentClass<IComponent>, IComponent> = new Map()
  public onComponentChanged?: (arg0: Entity) => void

  /**
   * 创建实体
   * @returns 实体
   */
  public static create(world: World, alias?: string) {
    const entity = this._pool.acquire()
    entity._world = world
    if (alias) {
      entity.alias = alias
    }
    return entity
  }

  /**
   * 销毁实体
   * @param entity 实体
   */
  public static destory(entity: Entity) {
    if (entity.isSingleton) {
      throw new Error('Cannot destroy singleton entities.')
    }
    entity._destory()
    this._pool.release(entity)
  }

  /**
   * 单例实体
   * @returns 实体
   */
  public static singleton(world: World) {
    const entity = new Entity()
    entity._world = world
    entity.alias = 'singleton components'
    entity.isSingleton = true

    return entity
  }

  /**
   * 实体池统计
   */
  public static poolStats() {
    return {
      size: this._pool.totalSize(),
      free: this._pool.totalFree(),
      used: this._pool.totalUsed()
    }
  }

  /** 销毁实体 */
  protected _destory() {
    this.alias = undefined
    this._world = undefined
    this.onComponentChanged = undefined
    this.removeAllComponent()
  }

  /**
   * 校验组件是否符合规范
   * @param componentClass 组件类
   * @returns true - 符合， false - 不符合
   */
  public verifyComponent(componentClass: IComponentClass<IComponent>) {
    // 如果组件没有继承基础组件类，或者组件没有注册，就必须报异常
    if (
      !componentClass._isComponent ||
      !componentClass._isRegister
    ) {
      throw new Error(`The Component does not meet specifications.\n` +
        `Entity: ${this.alias || this.id} Component: ${componentClass._id || componentClass.name}`)
    }
    // 如果组件已经添加过了，就报个警告
    if (this.hasComponent(componentClass)) {
      console.warn(`Component already exists on entity.\n` +
        `Entity: ${this.alias || this.id} Component: ${componentClass._id}`)
      return false
    } else {
      return true
    }
  }

  /**
   * 查询实体是否包含组件
   * @param componentClass 组件类
   * @returns true - 包含， false - 不包含
   */
  public hasComponent(componentClass: IComponentClass<IComponent>) {
    return this._components.has(componentClass)
  }

  /**
   * 查询实体是否包含一组组件
   * @param componentClassList 组件类数组
   * @returns true - 包含， false - 不包含
   */
  public hasAllComponent(componentClassList: IComponentClass<IComponent>[]) {
    for (const componentClass of componentClassList) {
      if (!this.hasComponent(componentClass)) {
        return false
      }
    }
    return true
  }

  /**
   * 查询实体是否包含一组组件中的任意一个组件
   * @param componentClassList 组件类类型数组
   * @returns true - 包含， false - 不包含
   */
  public hasAnyComponent(componentClassList: IComponentClass<IComponent>[]) {
    for (const componentClass of componentClassList) {
      if (this.hasComponent(componentClass)) {
        return true
      }
    }
    return false
  }

  /**
   * 获得组件
   * @param componentClass 组件类
   * @returns 组件数据
   */
  public getComponent<T extends IComponent>(componentClass: IComponentClass<T>) {
    if (this._components.has(componentClass)) {
      return this._components.get(componentClass) as T
    } else {
      console.warn('getComponent: Component not found.\n' +
        `Component: ${ componentClass }`)
      return {} as T
    }
  }

  /**
   * 添加组件
   * @param componentClass 组件类
   * @param values 覆盖默认组件状态
   * @returns this
   */
  public addComponent<T extends IComponent>(
    componentClass: IComponentClass<T>,
    values?: Partial<T>
  ) {
    if (!this.verifyComponent(componentClass)) {
      return this
    }
    const component = IComponent.create(this, componentClass, values)
    this._components.set(componentClass, component)
    this.onComponentChanged && this.onComponentChanged(this)
    return this
  }

  /**
   * 移除组件
   * @param componentClass 组件类类型
   * @returns this
   */
  public removeComponent(componentClass: IComponentClass<IComponent>) {
    if (this._components.has(componentClass)) {
      const component = this._components.get(componentClass)
      IComponent.destory(componentClass, component)
      this._components.delete(componentClass)
      this.onComponentChanged && this.onComponentChanged(this)
    }
    return this
  }

  /**
   * 移除全部组件
   * @returns this
   */
  public removeAllComponent() {
    this._components.forEach((component, key) => {
      IComponent.destory(key, component)
      this._components.delete(key)
    })
    this.onComponentChanged && this.onComponentChanged(this)
    return this
  }

  /**
   * 把实体转换为 json 数据
   * 
   * NOTE: 该方法会被 JSON.stringify() 隐式调用，该方法定义什么值将被序列化。
   * @returns 返回 json 数据
   */
  public toJSON() {
    const components: any[] = []
    this._components.forEach((component, componentClass) => {
      components.push({
        class: componentClass._id,
        content: component
      })
    })
    const json: any = {
      id: this.id,
      alias: this.alias,
      isSingleton: this.isSingleton,
      components
    }
    return json
  }

  /**
   * 从 json 数据还原实体
   * @param json json 数据
   */
  public fromJSON(json: any) {
    const componentClassMap = IComponent._componentClassMap
    this.id = json.id
    this.alias = json.alias
    this.isSingleton = json.isSingleton
    const components = json.components as any[]
    for (const item of components) {
      const classId = item.class
      const content = item.content
      if (!componentClassMap.has(classId)) {
        console.warn(`Cannot find component with id '${classId}'.`)
        continue
      }
      const componentClass = componentClassMap.get(classId)
      this.addComponent(componentClass, content)
    }
  }
}
