import { bgm, stage, scene, dialog, character, flow, util } from '../../api'
import { color } from '../../../config'

const commonStart = [
  stage.curtain.down(),
  scene.show('voiceover', 'voiceover'),
  stage.curtain.up(500),

  stage.voiceover.show(
    util.colorText('海神寺', color.deepskyblue)
  ),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.hide('voiceover'),
  scene.show('temple', 'map01004'),
  stage.curtain.up(500),

  bgm.play('shopping', {
    loop: true
  }),
]

const commonEnd = [
  bgm.stop('shopping'),
  stage.curtain.down(500),
  flow.goto('chapter2_2')
]

// 海神寺
export const chapter2_2_3 = [
  ...commonStart,

  dialog.show(
    util.colorText(
      [
        '我数着老旧的石阶，缓缓拾级而上，很快就来到了海神寺。',
        '虽然已经接近中午时分，但是已经有不少的游客甚至是当地人来海神寺参拜。',
        '我准备走向寺庙另一头的观景台，此时有个熟悉的身影吸引我的注意力。'
      ],
      color.lightskyblue
    )
  ),

  character.show('xing_sha_1'),

  dialog.show(
    [
      '那不是……昨天那个在这里不小心撞到的女孩子吗……？',
      '想不到又在这里遇到她了啊……。',
      '她是一个人吗？为什么脸上的神情看起来还是这么不快乐呢……？'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '带着哀伤神情的女孩，从海神寺的殿堂走了出来，旁边正在兜售印着漂亮纹饰护身符的小贩跟她打了声招呼。',
      color.lightskyblue
    )
  ),

  character.hide('xing_sha_1'),

  dialog.show(
    '哦~是小姐啊，今天又来拜拜了吗？',
    '小贩婆婆'
  ),

  character.show('xing_sha_2'),

  dialog.show(
    util.colorText(
      '哀伤女孩只是轻轻地点了点投，笑了笑便快步离去。',
      color.lightskyblue
    )
  ),

  character.hide('xing_sha_2'),

  dialog.show(
    util.colorText(
      [
        '说是笑，其实也太夸张了点，更贴切的说法不过是嘴角往上牵动了一下罢了。',
        '看着她离去的背影，我不自觉地耸耸肩，转个方向爬上了观景台。'
      ],
      color.lightskyblue
    )
  ),

  stage.curtain.down(500),
  dialog.hide(),
  scene.hide('temple'),
  scene.show('seaside', 'map01001'),
  stage.curtain.up(500),

  dialog.show(
    util.colorText(
      [
        '眼前辽阔的海景不自觉让人精神一振。',
        '旁边有几名高中生模样的家伙正喋喋不休地争吵着，听起来像是在打赌谁可以游上那个小岛。',
        '有几个还穿着跟我身上一模一样的T恤——Sawa亲手设计的。'
      ],
      color.lightskyblue
    )
  ),
  dialog.show(
    '是同民宿的房客吗……？',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '其中一个眼睛红肿，有些醉醺醺的家伙，口齿不清地嚷着失恋有什么了不起的话，还把身上的T恤都反过来穿，斗大的冲浪招生字样印在胸前，搭配上那张引人遐思的照片……',
      color.lightskyblue
    )
  ),
  dialog.show(
    '广告打的也太大了点吧，真是乱七八糟啊……',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '我看着那个醉的连路都走不稳的家伙，不断戳着胸口上的照片对着同伴宣示着，从今以后照片上的女人才是他心目中的女神，不禁在一旁轻笑道。',
      color.lightskyblue
    )
  ),
  dialog.show(
    '看来这趟旅程会很有趣啊，再去其他景点看看吧！',
    '以翔'
  ),

  ...commonEnd
]

