import { ISystem } from '../core'
import { TextComponent } from '../components'

export class TextUpdateSystem extends ISystem {
  public execute() {
    const entities = this.queries.entities.results

    for (const entity of entities) {
      const Text = entity.getComponent(TextComponent)
      if (Text.textView.text !== Text.text) {
        Text.textView.text = Text.text
        Text.justifyContent()
      }
    }
  }
}

TextUpdateSystem.queries = {
  entities: [TextComponent]
}
