import {
  TransformComponent,
  TransparentComponent,
  DisplacementComponent,
  FadeComponent
} from '../../../../engine/components'
import { RemoveComponent } from '../../../components/tag'
import { group, addTransform } from './util'

function configOption(options) {
  options = options || {}

  if (options.animation) {
    options.animation = {
      duration: 1000,
      ...options.animation
    }
  }

  return options
}

function getEntity(world, id) {
  const entityId = group.get(world, id)

  if (!entityId) {
    return null
  }

  const entity = world.getEntity(entityId)

  if (!entity) {
    return null
  }

  return entity
}

function setEntity(world, id, options) {
  options = configOption(options)
  let { render, animation } = options
  const entity = getEntity(world, id)

  if (entity === null) {
    return
  }

  const Transform = entity.getComponent(TransformComponent)
  const Transparent = entity.getComponent(TransparentComponent)

  render = {
    position: {
      x: Transform.x,
      y: Transform.y,
    },
    alpha: Transparent.alpha,
    ...render
  }

  if (!animation) {
    return
  }

  const { position, alpha, duration = 1000 } = animation

  if (position) {
    addTransform(
      entity,
      DisplacementComponent,
      render.position,
      position,
      duration
    )
  }

  if (typeof alpha === 'number') {
    addTransform(
      entity,
      FadeComponent,
      { alpha: render.alpha },
      { alpha },
      duration
    )
  }
}

export function hide(id, options) {
  function init(world) {
    setEntity(world, id, options)
  }

  function process(world) {
    const entity = getEntity(world, id)

    if (entity === null) {
      return true
    } else {
      group.delete(world, id)
      entity.addComponent(RemoveComponent)
      return true
    }
  }

  return {
    init,
    process
  }
}

export function hide_async(id, options) {
  function init(world) {
    setEntity(world, id, options)
  }

  function process(world) {
    const entity = getEntity(world, id)

    if (entity === null) {
      return true
    } else {
      if (
        entity.hasComponent(DisplacementComponent) ||
        entity.hasComponent(FadeComponent)
      ) {
        return false
      } else {
        group.delete(world, id)
        world.removeEntity(entity)
        return true
      }
    }
  }

  return {
    init,
    process
  }
}
