import { createComponent, group } from './util'

export function update(id, index, component) {
  function init(world) {
    const entityIdList = group.get(world, id)
  
    if (typeof entityIdList !== 'undefined') {
      const item = entityIdList[index]
      let entity = world.getEntity(item)
      world.removeEntity(entity)
      entity = createComponent(world, component)
      entityIdList[index] = entity.id
    }
  }

  function process() {
    return true
  }

  return {
    init,
    process
  }
}