import { IComponent, componentRegister } from '../core'
import { Sound } from '../sound'

type PlayOptions = {
  start?: number
  end?: number
  speed?: number
  loop?: boolean
  sprite?: string
  muted?: boolean
}

export class SoundComponent extends IComponent {
  public alias: string
  public options: PlayOptions

  public firstPlay: boolean
  public isPlaying: boolean
  public sound: Sound

  constructor() {
    super()
    this.alias = null
    this.options = {}

    this.firstPlay = true
    this.isPlaying = false
    this.sound = null
  }

  init() {
    this.sound = new Sound(this.alias)
  }

  clear() {
    this.alias = null
    this.options = {}

    this.firstPlay = true
    this.isPlaying = false
    this.sound.stop()
    this.sound = null
  }

  toJSON() {
    const { alias, options } = this
    return { alias, options }
  }
}

componentRegister('SoundComponent')(SoundComponent)
