import { World } from '../core'
import { Stage } from './stage'

export class Scene extends World {
  public view: Stage

  constructor() {
    super()
    this.view = new Stage()
    // 预设11个用户图层
    for (let i = 0; i <= 10; i++) {
      this.addLayer(`UserLayer${i}`)
    }
  }

  public addLayer(id: string, index?: number) {
    this.view.createLayer(id, index)
  }

  public addParticleLayer(id: string, index?: number) {
    this.view.createParticleLayer(id, index)
  }

  public hasLayer(id: string) {
    return this.view.hasLayer(id)
  }

  public getLayer(id: string) {
    return this.view.getLayer(id)
  }

  public removeLayer(id: string) {
    this.view.removeLayer(id)
  }

  public addSprite(layerId: string, sprite: PIXI.Container) {
    const layer = this.getLayer(layerId)
    layer?.addChild(sprite)
  }

  public removeSprite(layerId: string, sprite: PIXI.Container) {
    const layer = this.getLayer(layerId)
    layer?.removeChild(sprite)
  }
}
