import { ISystem } from '../../engine/core'
import { InputComponent } from '../components/singleton'

export class StorySystem extends ISystem {
  execute(delta) {
    const singleton = this.world.singleton

    if (this.world.systemData && this.world.storyScript) {
      const Input = singleton.getComponent(InputComponent)
      let result = true
      while (result) {
        result = this.processStory(delta)
        // 合并多条命令的时候，需要重制下 input ，防止对后面的命令造成影响
        Input.isClick = false
      }
    }
  }

  processStory(delta) {
    if (typeof this.world.storyScript === 'undefined') {
      return false
    }

    const system = this.world.systemData
    const chapter = this.world.storyScript[system.chapter]

    if (typeof chapter === 'undefined') {
      return false
    }

    const nodeCount = chapter.length
    const node = chapter[system.nodeIndex]

    if (typeof node === 'undefined') {
      return false
    }

    if (system.initialed === false && node.init) {
      node.init(this.world)
      system.initialed = true
    }

    const result = node.process(this.world, delta)

    if (result === true) {
      if (node.goto) {
        // 如果节点存在 goto 命令，就开始进行跳转
        const goto = node.goto(this.world)

        if (
          goto && (
          typeof goto.chapter !== 'undefined' ||
          typeof goto.node !== 'undefined')
        ) {
          system.initialed = false
          system.subIndex = 0

          if (
            typeof goto.chapter !== 'undefined' &&
            goto.chapter !== system.chapter
          ) {
            // 章节跳转时清除全部实体
            this.world.removeAllEntity()
            system.entities = {}

            system.nodeIndex = 0
            system.chapter = goto.chapter
          }
          if (typeof goto.node !== 'undefined') {
            system.nodeIndex = goto.node
          }
        } else if (system.nodeIndex < nodeCount) {
          system.nodeIndex++
          system.subIndex = 0
          system.initialed = false
        }

      } else if (system.nodeIndex < nodeCount) {
        system.nodeIndex++
        system.subIndex = 0
        system.initialed = false
      }
    }

    return result
  }
}
