import { ISystem } from '../core'
import { AnimationComponent } from '../components'

export class AnimationSystem extends ISystem {
  public execute() {
    const entities = this.queries.entities.results

    for (const entity of entities) {
      const animation = entity.getComponent(AnimationComponent)
      const view = animation.view as PIXI.AnimatedSprite

      if (view.loop !== animation.loop) {
        view.loop = animation.loop
      }

      if (view.animationSpeed !== animation.animationSpeed) {
        view.animationSpeed = animation.animationSpeed
      }

      if (animation.playing === true) {
        if (view.playing === false) {
          view.play()
        }
      } else {
        if (view.playing === true) {
          view.gotoAndStop(0)
        }
      }
    }
  }
}

AnimationSystem.queries = {
  entities: [AnimationComponent]
}
