import { ISystem } from '../core'
import { SoundComponent } from '../components'

export class SoundSystem extends ISystem {
  public execute() {
    const entities = this.queries.entities.results

    for (const entity of entities) {
      const Sound = entity.getComponent(SoundComponent)

      if (Sound.firstPlay === true) {
        // 说明音乐还没开始播放，把声音放起来
        Sound.sound.play(Sound.options)
        Sound.firstPlay = false
        Sound.isPlaying = true
      } else {
        if (Sound.isPlaying === true) {
          if (Sound.sound.isPlaying === false) {
            // 说明音乐已经放完了，移除声音组件
            entity.removeComponent(SoundComponent)
          }
        } else {
          // 主动停止播放，并移除组件
          Sound.sound.stop()
          entity.removeComponent(SoundComponent)
        }
      }
    }
  }
}

SoundSystem.queries = {
  entities: [SoundComponent]
}
