import {
  SpriteComponent,
  TransparentComponent,
  FadeComponent
} from '../../../../engine/components'
import { group } from './util'

function createCurtain(world, duration, alpha) {
  const entity = world.createEntity()
    .addComponent(SpriteComponent, {
      layer: 'UserLayer10',
      textureId: 'curtain'
    })
  
  if (duration > 0) {
    entity
      .addComponent(TransparentComponent, {
        alpha: 1 - alpha
      })
      .addComponent(FadeComponent, {
        targetId: 'TransparentComponent',
        start: {
          alpha: 1 - alpha
        },
        end: {
          alpha
        },
        duration
      })
  } else {
    entity
      .addComponent(TransparentComponent, {
        alpha
      })
  }

  return entity
}

export function up(duration = 0) {
  function init(world) {
    const entityId = group.get(world, 'curtain')
    if (!entityId) {
      const entity = createCurtain(world, duration, 0)
      group.set(world, 'curtain', entity.id)
      return
    }

    const entity = world.getEntity(entityId)
    if (entity.hasComponent(FadeComponent)) {
      entity.removeComponent(FadeComponent)
    }
    const Transparent = entity.getComponent(TransparentComponent)

    if (duration > 0) {
      Transparent.alpha = 1
      entity.addComponent(FadeComponent, {
        targetId: 'TransparentComponent',
        start: {
          alpha: 1
        },
        end: {
          alpha: 0
        },
        duration
      })
    } else {
      Transparent.alpha = 0
    }
  }

  function process(world) {
    const entityId = group.get(world, 'curtain')

    if (!entityId) {
      return true
    }

    const entity = world.getEntity(entityId)
    if (entity.hasComponent(FadeComponent)) {
      return false
    } else {
      group.delete(world, 'curtain')
      world.removeEntity(entity)
      return true
    }
  }

  return {
    init,
    process
  }
}

export function down(duration = 0) {
  function init(world) {
    const entityId = group.get(world, 'curtain')
    if (entityId) {
      return
    }

    const entity = createCurtain(world, duration, 1)
    group.set(world, 'curtain', entity.id)
  }

  function process(world) {
    const entityId = group.get(world, 'curtain')

    if (!entityId) {
      return true
    }

    const entity = world.getEntity(entityId)
    if (entity.hasComponent(FadeComponent)) {
      return false
    } else {
      return true
    }
  }

  return {
    init,
    process
  }
}
