import { AnimatedSprite, Texture } from 'pixi.js'
import RenderComponent from './base/render'
import { componentRegister } from '../core'

export class AnimationComponent extends RenderComponent {
  // serialize fields
  public texturesId: string[]
  public loop: boolean
  public animationSpeed: number
  public playing: boolean
  /** 动画周期 */
  public cycle: number
  // temp fields
  public textures: Texture[]

  constructor() {
    super()
    this.texturesId = null
    this.loop = true
    this.animationSpeed = 1
    this.playing = true
    this.cycle = 0

    this.textures = null
  }

  public init() {
    if (this.texturesId === null) {
      this.textures = [Texture.EMPTY]
    } else {
      const count = this.texturesId.length
      const fillFrames = this.getFillFrames()
      this.textures = []

      for (let i = 0; i < fillFrames - count; i++) {
        const texture = Texture.from(this.texturesId[0])
        this.textures.push(texture)
      }
      for (let i = 0; i < count; i++) {
        const texture = Texture.from(this.texturesId[i])
        this.textures.push(texture)
      }
    }

    this.view = new AnimatedSprite(this.textures)
  }

  /**
   * 根据动画周期计算补帧帧数
   * 
   * @returns {number}
   */
  public getFillFrames(): number {
    return this.cycle * this.animationSpeed / 1000 * 60
  }

  public clear() {
    this.revert()
    this.texturesId = null
    this.loop = true
    this.animationSpeed = 1
    this.playing = true
    this.cycle = 0

    this.textures = null
  }

  public toJSON() {
    const { texturesId, loop, animationSpeed, playing, cycle } = this
    const render = this.getRenderJSON()

    return {
      texturesId, loop, animationSpeed, playing, cycle,
      ...render
    }
  }
}

componentRegister('AnimationComponent')(AnimationComponent)
