import { bgm, stage, choice, flow } from '../../api'

const common = [
  bgm.stop('nerves'),
  stage.curtain.down(500),
]

const temple = [
  ...common,
  flow.goto('chapter2_5')
]

const park = [
  ...common,
  flow.goto('chapter2_4_4')
]

const hostel = [
  ...common,
  flow.goto('chapter2_4_5')
]

// 海风西街
export const west = [
  choice.select(
    [
      '海神寺',
      '帆船公园',
      '彩虹天堂',
      '取消'
    ],
    [
      (data, index) => {
        data.roadSign.temple++
        return {
          node: index + 1
        }
      },
      (data, index) => {
        data.roadSign.park++
        return {
          node: index + 1 + temple.length
        }
      },
      (data, index) => {
        data.roadSign.hostel++
        return {
          node: index + 1 + temple.length + park.length
        }
      },
      data => {
        return {
          node: data.roadSign.index
        }
      }
    ]
  ),

  ...temple,
  ...park,
  ...hostel
]
