import { World } from './world'
import { EntityManager } from './entity-manager'
import { ISystemClass, ISystem, SystemPriority } from './system'
import { now } from './utils'

export class SystemManager {
  private systems: ISystem[] = []

  constructor(
    private world: World,
    private entityManager: EntityManager
  ) {}
  
  /**
   * 向 system 中注入查询
   * @param systemClass
   * @param system
   */
  private _injectQuery<T extends ISystem>(
    systemClass: ISystemClass<T>,
    system: ISystem
  ) {
    const queries = systemClass.queries

    if (queries) {
      for (const key in queries) {
        // 偷懒写法，如果是数组，那就当作是"完全匹配查询"
        // 如果是 Object，那就判断下属性是否符合要求，不符合就跳过
        if (Array.isArray(queries[key])) {
          const query = this.entityManager.createQuery(queries[key] as any, 'all')
          system.queries[key] = {
            results: query.getEntity()
          }
        } else {
          const { type = 'all', components } = queries[key] as any
          if (type !== 'all' && type !== 'any' && Array.isArray(components)) {
            continue
          }
          const query = this.entityManager.createQuery(components, type)
          system.queries[key] = {
            results: query.getEntity()
          }
        }
      }
    }
  }

  /**
   * 把 system 放进 system 队列
   * @param system 
   */
  private _pushSystem(system: ISystem) {
    const systemCount = this.systems.length

    if (systemCount > 0) {
      if (system._priority < this.systems[0]._priority) {
        this.systems.unshift(system)
        return
      } else if (system._priority >= this.systems[systemCount - 1]._priority) {
        this.systems.push(system)
        return
      }
    }

    for (let i = 0; i < systemCount; i++) {
      if (system._priority < this.systems[i]._priority) {
        this.systems.splice(i, 0, system)
        return
      }
    }
    this.systems.push(system)
  }

  /**
   * 注册 system
   * @param systemClass system 类
   * @param priority system 优先级
   */
  public registerSystem<T extends ISystem>(
    systemClass: ISystemClass<T>,
    priority: number = SystemPriority.Update
  ) {
    if (!systemClass.isSystem) {
      throw new Error(`System "${systemClass.name}" must extend "System" class.`)
    }
    if (this.isRegisterSystem(systemClass)) {
      console.warn(`System "${systemClass.name}" already registered.`)
      return
    }
    const system = new systemClass()
    system._priority = priority
    system.world = this.world
    this._injectQuery(systemClass, system)
    this._pushSystem(system)
    system.init()
  }

  /**
   * 判断 system 是否已经注册
   * @param systemClass
   * @returns true - 已注册，false - 未注册
   */
  public isRegisterSystem<T extends ISystem>(systemClass: ISystemClass<T>) {
    return this.systems.findIndex(system => system instanceof systemClass) !== -1
  }

  /**
   * 执行 system
   * @param delta 时间差
   * @param timestamp 时间戳
   */
  public execute(delta: number, timestamp: number) {
    this.systems.forEach(system => {
      const startTime = now()
      system.execute(delta, timestamp)
      this.entityManager.updateEntityQuery()
      system._executeTime = now() - startTime
    })
  }

  /**
   * 统计 System 数据
   * @returns 统计数据
   */
  public stats() {
    return {
      numSystems: this.systems.length,
      executeTime: this.systems.map(system => {
        const systemClass = system.constructor
        return {
          name: systemClass.name,
          executeTime: system._executeTime
        }
      })
    }
  }
}
