import { IComponent, componentRegister } from '../core'

type Vars = {
  alpha?: number
}

/**
 * 淡入淡出组件
 */
export class FadeComponent extends IComponent {
  public start: Vars
  public end: Vars
  public duration: number

  constructor() {
    super()
    this.start = {}
    this.end = {}
    this.duration = 0
  }

  public clear() {
    this.start = {}
    this.end = {}
    this.duration = 0
  }

  public toJSON() {
    const { start, end, duration } = this
    return { start, end, duration }
  }
}

componentRegister('FadeComponent')(FadeComponent)
