import { Entity } from './entity'
import { IComponentClass, IComponent } from './component'
import { World } from './world'

export type ISystemQuery = {
  [queryName: string]: {
    type: 'all' | 'any'
    components: IComponentClass<IComponent>[]
  } | IComponentClass<IComponent>[]
}

export type ISystemQueries = {
  [queryName: string]: {
    results: Entity[]
  }
}

export type ISystemClass<T extends ISystem> = {
  new (...args: any[]): T
  isSystem: true
  queries?: ISystemQuery
}

/**
 * 系统优先级
 */
export const SystemPriority = {
  Begin: 0,
  Start: 1,
  FixedUpdate: 2,
  Update: 3,
  Animation: 4,
  LateUpdate: 5,
  BeforeRenderer: 6,
  Renderer: 7,
  End: 8
}

export class ISystem {
  public static readonly isSystem: true = true
  public static queries?: ISystemQuery
  public world: World = null
  public queries: ISystemQueries = {}
  public _priority: number = -1
  public _executeTime: number = 0
  public init() {}
  public execute(delta: number, timestamp: number) {}
}
