import { SoundComponent } from '../../../../engine/components'
import { BGMComponent } from '../../../components/tag'
import { group } from './util'

function play(alias, options) {
  options = options || {}

  function init(world) {
    const entityId = group.get(world, alias)

    if (entityId) {
      return
    }

    const entity = world.createEntity()
      .addComponent(BGMComponent)
      .addComponent(SoundComponent, {
        alias,
        options
      })

    group.set(world, alias, entity.id)
  }

  function process() {
    return true
  }

  return {
    init,
    process
  }
}

function stop(alias) {
  function init(world) {
    const entityId = group.get(world, alias)

    if (entityId) {
      const entity = world.getEntity(entityId)
      const Sound = entity.getComponent(SoundComponent)
      Sound.isPlaying = false
      group.delete(world, alias)
    }
  }

  function process() {
    return true
  }

  return {
    init,
    process
  }
}

export default {
  play,
  stop
}
