import type { AbstractRenderer } from 'pixi.js'
import { InteractionData, Point } from './InteractionData'
import type { InteractivePointerEvent } from './InteractionData'

const MOUSE_POINTER_ID = 1

interface CrossCSSStyleDeclaration extends CSSStyleDeclaration {
  msContentZooming: string
  msTouchAction: string
}

export class InteractionManager {
  /**
   * Does the device support touch events
   * https://www.w3.org/TR/touch-events/
   *
   * @readonly
   * @member {boolean}
   */
  public readonly supportsTouchEvents: boolean

  /**
   * Does the device support pointer events
   * https://www.w3.org/Submission/pointer-events/
   *
   * @readonly
   * @member {boolean}
   */
  public readonly supportsPointerEvents: boolean

  /**
   * The renderer this interaction manager works for.
   *
   * @member {PIXI.AbstractRenderer}
   */
  public renderer: AbstractRenderer

  /**
   * The mouse data
   *
   * @member {InteractionData}
   */
  public mouse: InteractionData

  /**
   * The current resolution / device pixel ratio.
   *
   * @member {number}
   * @default 1
   */
  public resolution: number

  /**
   * The DOM element to bind to.
   *
   * @protected
   * @member {HTMLElement}
   */
  protected interactionDOMElement: HTMLElement

  /**
   * Have events been attached to the dom element?
   *
   * @protected
   * @member {boolean}
   */
  protected eventsAdded: boolean

  /**
   * @param {PIXI.CanvasRenderer|PIXI.Renderer} renderer - A reference to the current renderer
   */
  constructor(renderer: AbstractRenderer) {
    this.renderer = renderer
    this.mouse = new InteractionData()
    this.mouse.identifier = MOUSE_POINTER_ID
    this.mouse.global.set(-1)
    this.interactionDOMElement = null
    this.eventsAdded = false
    this.supportsTouchEvents = 'ontouchstart' in window
    this.supportsPointerEvents = !!window.PointerEvent
    this.resolution = 1

    // this will make it so that you don't have to call bind all the time
    this.onPointerUp = this.onPointerUp.bind(this)
    this.onPointerCancel = this.onPointerCancel.bind(this)
    this.onPointerDown = this.onPointerDown.bind(this)
    this.onPointerMove = this.onPointerMove.bind(this)
    this.onPointerOut = this.onPointerOut.bind(this)
    this.onPointerOver = this.onPointerOver.bind(this)

    this.setTargetElement(this.renderer.view, this.renderer.resolution)
  }

  /**
   * Sets the DOM element which will receive mouse/touch events. This is useful for when you have
   * other DOM elements on top of the renderers Canvas element. With this you'll be bale to delegate
   * another DOM element to receive those events.
   *
   * @param {HTMLElement} element - the DOM element which will receive mouse and touch events.
   * @param {number} [resolution=1] - The resolution / device pixel ratio of the new element (relative to the canvas).
   */
  public setTargetElement(element: HTMLElement, resolution: number = 1): void {
    this.removeEvents()

    this.interactionDOMElement = element

    this.resolution = resolution

    this.addEvents()
  }

  /**
   * Registers all the DOM events
   *
   * @private
   */
  private addEvents(): void {
    if (this.eventsAdded || !this.interactionDOMElement) {
      return
    }

    const style = this.interactionDOMElement.style as CrossCSSStyleDeclaration

    if (window.navigator.msPointerEnabled) {
      style.msContentZooming = 'none'
      style.msTouchAction = 'none'
    } else if (this.supportsPointerEvents) {
      style.touchAction = 'none'
    }

    /*
     * These events are added first, so that if pointer events are normalized, they are fired
     * in the same order as non-normalized events. ie. pointer event 1st, mouse / touch 2nd
     */
    const dom = this.interactionDOMElement
    if (this.supportsPointerEvents) {
      window.document.addEventListener('pointermove', this.onPointerMove, true)
      dom.addEventListener('pointerdown', this.onPointerDown, true)
      // pointerout is fired in addition to pointerup (for touch events) and pointercancel
      // we already handle those, so for the purposes of what we do in onPointerOut, we only
      // care about the pointerleave event
      dom.addEventListener('pointerleave', this.onPointerOut, true)
      dom.addEventListener('pointerover', this.onPointerOver, true)
      window.addEventListener('pointercancel', this.onPointerCancel, true)
      window.addEventListener('pointerup', this.onPointerUp, true)
    } else {
      window.document.addEventListener('mousemove', this.onPointerMove, true)
      dom.addEventListener('mousedown', this.onPointerDown, true)
      dom.addEventListener('mouseout', this.onPointerOut, true)
      dom.addEventListener('mouseover', this.onPointerOver, true)
      window.addEventListener('mouseup', this.onPointerUp, true)
    }

    // always look directly for touch events so that we can provide original data
    // In a future version we should change this to being just a fallback and rely solely on
    // PointerEvents whenever available
    if (this.supportsTouchEvents) {
      dom.addEventListener('touchstart', this.onPointerDown, true)
      dom.addEventListener('touchcancel', this.onPointerCancel, true)
      dom.addEventListener('touchend', this.onPointerUp, true)
      dom.addEventListener('touchmove', this.onPointerMove, true)
    }

    this.eventsAdded = true
  }

  /**
   * Removes all the DOM events that were previously registered
   *
   * @private
   */
  private removeEvents(): void {
    if (!this.eventsAdded || !this.interactionDOMElement) {
      return
    }

    const style = this.interactionDOMElement.style as CrossCSSStyleDeclaration

    if (window.navigator.msPointerEnabled) {
      style.msContentZooming = ''
      style.msTouchAction = ''
    } else if (this.supportsPointerEvents) {
      style.touchAction = ''
    }

    const dom = this.interactionDOMElement
    if (this.supportsPointerEvents) {
      window.document.removeEventListener('pointermove', this.onPointerMove, true)
      dom.removeEventListener('pointerdown', this.onPointerDown, true)
      dom.removeEventListener('pointerleave', this.onPointerOut, true)
      dom.removeEventListener('pointerover', this.onPointerOver, true)
      window.removeEventListener('pointercancel', this.onPointerCancel, true)
      window.removeEventListener('pointerup', this.onPointerUp, true)
    } else {
      window.document.removeEventListener('mousemove', this.onPointerMove, true)
      dom.removeEventListener('mousedown', this.onPointerDown, true)
      dom.removeEventListener('mouseout', this.onPointerOut, true)
      dom.removeEventListener('mouseover', this.onPointerOver, true)
      window.removeEventListener('mouseup', this.onPointerUp, true)
    }

    if (this.supportsTouchEvents) {
      dom.removeEventListener('touchstart', this.onPointerDown, true)
      dom.removeEventListener('touchcancel', this.onPointerCancel, true)
      dom.removeEventListener('touchend', this.onPointerUp, true)
      dom.removeEventListener('touchmove', this.onPointerMove, true)
    }

    this.interactionDOMElement = null

    this.eventsAdded = false
  }

  /**
   * Maps x and y coords from a DOM object and maps them correctly to the PixiJS view. The
   * resulting value is stored in the point. This takes into account the fact that the DOM
   * element could be scaled and positioned anywhere on the screen.
   *
   * @param  {number} x - the x coord of the position to map
   * @param  {number} y - the y coord of the position to map
   */
  public mapPositionToPoint(x: number, y: number): Point {
    const point = new Point(0)
    let rect

    // IE 11 fix
    if (!this.interactionDOMElement.parentElement) {
      rect = { x: 0, y: 0, width: 0, height: 0 }
    } else {
      rect = this.interactionDOMElement.getBoundingClientRect()
    }

    const resolutionMultiplier = 1.0 / this.resolution

    point.x =
      (x - rect.left) *
      ((this.interactionDOMElement as any).width / rect.width) *
      resolutionMultiplier
    point.y =
      (y - rect.top) *
      ((this.interactionDOMElement as any).height / rect.height) *
      resolutionMultiplier

    return point
  }

  /**
   * Process mouse event.
   * @param event 
   * @param isDown
   */
  public processInteractive(event: PointerEvent, isDown?: boolean): void {
    const interactionData = this.getInteractionData(event)

    if (typeof isDown === 'boolean') {
      interactionData.isDown = isDown
    } else {
      interactionData.isDown = this.mouse.isDown
    }

    this.configureInteractionDataForDOMEvent(event, interactionData)

    this.mouse = interactionData
  }

  /**
   * Is called when the pointer button is pressed down on the renderer element
   *
   * @private
   * @param {PointerEvent} originalEvent - The DOM event of a pointer button being pressed down
   */
  private onPointerDown(originalEvent: InteractivePointerEvent): void {
    // if we support touch events, then only use those for touch events, not pointer events
    if (
      this.supportsTouchEvents &&
      (originalEvent as PointerEvent).pointerType === 'touch'
    ) {
      return
    }

    const events = this.normalizeToPointerData(originalEvent)

    /*
     * No need to prevent default on natural pointer events, as there are no side effects
     * Normalized events, however, may have the double mousedown/touchstart issue on the native android browser,
     * so still need to be prevented.
     */

    // Guaranteed that there will be at least one event in events, and all events must have the same pointer type

    const event = events[0]

    // if ((event as any).isNormalized) {
    //   const cancelable =
    //     originalEvent.cancelable || !('cancelable' in originalEvent)

    //   if (cancelable) {
    //     originalEvent.preventDefault()
    //   }
    // }

    this.processInteractive(event, true)
  }

  /**
   * Is called when the pointer button is released on the renderer element
   *
   * @private
   * @param {PointerEvent} originalEvent - The DOM event of a pointer button being released
   */
  private onPointerComplete(originalEvent: InteractivePointerEvent): void {
    const events = this.normalizeToPointerData(originalEvent)

    const event = events[0]

    this.processInteractive(event, false)
  }

  /**
   * Is called when the pointer button is cancelled
   *
   * @private
   * @param {PointerEvent} event - The DOM event of a pointer button being released
   */
  private onPointerCancel(event: InteractivePointerEvent): void {
    // if we support touch events, then only use those for touch events, not pointer events
    if (
      this.supportsTouchEvents &&
      (event as PointerEvent).pointerType === 'touch'
    ) {
      return
    }

    this.onPointerComplete(event)
  }

  /**
   * Is called when the pointer button is released on the renderer element
   *
   * @private
   * @param {PointerEvent} event - The DOM event of a pointer button being released
   */
  private onPointerUp(event: InteractivePointerEvent): void {
    // if we support touch events, then only use those for touch events, not pointer events
    if (
      this.supportsTouchEvents &&
      (event as PointerEvent).pointerType === 'touch'
    ) {
      return
    }

    this.onPointerComplete(event)
  }

  /**
   * Is called when the pointer moves across the renderer element
   *
   * @private
   * @param {PointerEvent} originalEvent - The DOM event of a pointer moving
   */
  private onPointerMove(originalEvent: InteractivePointerEvent): void {
    // if we support touch events, then only use those for touch events, not pointer events
    if (
      this.supportsTouchEvents &&
      (originalEvent as PointerEvent).pointerType === 'touch'
    ) {
      return
    }

    const events = this.normalizeToPointerData(originalEvent)

    const event = events[0]

    this.processInteractive(event)
  }

  /**
   * Is called when the pointer is moved out of the renderer element
   *
   * @private
   * @param {PointerEvent} originalEvent - The DOM event of a pointer being moved out
   */
  private onPointerOut(originalEvent: InteractivePointerEvent): void {
    // if we support touch events, then only use those for touch events, not pointer events
    if (
      this.supportsTouchEvents &&
      (originalEvent as PointerEvent).pointerType === 'touch'
    ) {
      return
    }

    const events = this.normalizeToPointerData(originalEvent)

    // Only mouse and pointer can call onPointerOut, so events will always be length 1
    const event = events[0]

    this.processInteractive(event)
  }

  /**
   * Is called when the pointer is moved into the renderer element
   *
   * @private
   * @param {PointerEvent} originalEvent - The DOM event of a pointer button being moved into the renderer view
   */
  private onPointerOver(originalEvent: InteractivePointerEvent): void {
    const events = this.normalizeToPointerData(originalEvent)

    // Only mouse and pointer can call onPointerOver, so events will always be length 1
    const event = events[0]

    this.processInteractive(event)
  }

  /**
   * Get InteractionData.
   *
   * @private
   * @param {PointerEvent} event - Normalized pointer event, output from normalizeToPointerData
   * @return {InteractionData} - Interaction data for the given pointer identifier
   */
  private getInteractionData(event: PointerEvent): InteractionData {
    const pointerId = event.pointerId

    const interactionData = new InteractionData()
    interactionData.identifier = pointerId

    // copy properties from the event, so that we can make sure that touch/pointer specific
    // data is available
    interactionData.copyEvent(event)

    return interactionData
  }

  /**
   * Configure DOM PointerEvent and InteractionData
   *
   * @private
   * @param {PointerEvent} pointerEvent - The DOM event that will be paired with the InteractionEvent
   * @param {PIXI.InteractionData} interactionData - The InteractionData that will be paired
   *        with the InteractionEvent
   */
  private configureInteractionDataForDOMEvent(
    pointerEvent: PointerEvent,
    interactionData: InteractionData
  ): void {
    interactionData.global = this.mapPositionToPoint(
      pointerEvent.clientX,
      pointerEvent.clientY
    )

    // Not really sure why this is happening, but it's how a previous version handled things
    if (pointerEvent.pointerType === 'touch') {
      ;(pointerEvent as any).globalX = interactionData.global.x
      ;(pointerEvent as any).globalY = interactionData.global.y
    }

    interactionData.originalEvent = pointerEvent
  }

  /**
   * Ensures that the original event object contains all data that a regular pointer event would have
   *
   * @private
   * @param {TouchEvent|MouseEvent|PointerEvent} event - The original event data from a touch or mouse event
   * @return {PointerEvent[]} An array containing a single normalized pointer event, in the case of a pointer
   *  or mouse event, or a multiple normalized pointer events if there are multiple changed touches
   */
  private normalizeToPointerData(
    event: InteractivePointerEvent
  ): PointerEvent[] {
    const normalizedEvents = []

    if (this.supportsTouchEvents && event instanceof TouchEvent) {
      for (let i = 0, li = event.changedTouches.length; i < li; i++) {
        const touch = event.changedTouches[i] as TouchPointerEvent

        if (typeof touch.button === 'undefined')
          touch.button = event.touches.length ? 1 : 0
        if (typeof touch.buttons === 'undefined')
          touch.buttons = event.touches.length ? 1 : 0
        if (typeof touch.isPrimary === 'undefined') {
          touch.isPrimary =
            event.touches.length === 1 && event.type === 'touchstart'
        }
        if (typeof touch.width === 'undefined') touch.width = touch.radiusX || 1
        if (typeof touch.height === 'undefined')
          touch.height = touch.radiusY || 1
        if (typeof touch.tiltX === 'undefined') touch.tiltX = 0
        if (typeof touch.tiltY === 'undefined') touch.tiltY = 0
        if (typeof touch.pointerType === 'undefined')
          touch.pointerType = 'touch'
        if (typeof touch.pointerId === 'undefined')
          touch.pointerId = touch.identifier || 0
        if (typeof touch.pressure === 'undefined')
          touch.pressure = touch.force || 0.5
        if (typeof touch.twist === 'undefined') touch.twist = 0
        if (typeof touch.tangentialPressure === 'undefined')
          touch.tangentialPressure = 0
        // TODO: Remove these, as layerX/Y is not a standard, is deprecated, has uneven
        // support, and the fill ins are not quite the same
        // offsetX/Y might be okay, but is not the same as clientX/Y when the canvas's top
        // left is not 0,0 on the page
        if (typeof touch.layerX === 'undefined')
          touch.layerX = touch.offsetX = touch.clientX
        if (typeof touch.layerY === 'undefined')
          touch.layerY = touch.offsetY = touch.clientY

        // mark the touch as normalized, just so that we know we did it
        touch.isNormalized = true

        normalizedEvents.push(touch)
      }
    }
    // apparently PointerEvent subclasses MouseEvent, so yay
    else if (
      event instanceof MouseEvent &&
      (!this.supportsPointerEvents || !(event instanceof window.PointerEvent))
    ) {
      const tempEvent = event as MousePointerEvent

      if (typeof tempEvent.isPrimary === 'undefined') tempEvent.isPrimary = true
      if (typeof tempEvent.width === 'undefined') tempEvent.width = 1
      if (typeof tempEvent.height === 'undefined') tempEvent.height = 1
      if (typeof tempEvent.tiltX === 'undefined') tempEvent.tiltX = 0
      if (typeof tempEvent.tiltY === 'undefined') tempEvent.tiltY = 0
      if (typeof tempEvent.pointerType === 'undefined')
        tempEvent.pointerType = 'mouse'
      if (typeof tempEvent.pointerId === 'undefined')
        tempEvent.pointerId = MOUSE_POINTER_ID
      if (typeof tempEvent.pressure === 'undefined') tempEvent.pressure = 0.5
      if (typeof tempEvent.twist === 'undefined') tempEvent.twist = 0
      if (typeof tempEvent.tangentialPressure === 'undefined')
        tempEvent.tangentialPressure = 0

      // mark the mouse event as normalized, just so that we know we did it
      tempEvent.isNormalized = true

      normalizedEvents.push(tempEvent)
    } else {
      normalizedEvents.push(event)
    }

    return normalizedEvents as PointerEvent[]
  }

  /**
   * Destroys the interaction manager
   *
   */
  public destroy(): void {
    this.removeEvents()

    this.renderer = null

    this.mouse = null

    this.interactionDOMElement = null

    this.onPointerDown = null

    this.onPointerUp = null

    this.onPointerCancel = null

    this.onPointerMove = null

    this.onPointerOut = null

    this.onPointerOver = null
  }
}

interface MousePointerEvent extends PointerEvent {
  isPrimary: boolean
  width: number
  height: number
  tiltX: number
  tiltY: number
  pointerType: string
  pointerId: number
  pressure: number
  twist: number
  tangentialPressure: number
  isNormalized: boolean
}

interface TouchPointerEvent extends Touch {
  button: number
  buttons: number
  isPrimary: boolean
  width: number
  height: number
  tiltX: number
  tiltY: number
  pointerType: string
  pointerId: number
  pressure: number
  twist: number
  tangentialPressure: number
  layerX: number
  layerY: number
  offsetX: number
  offsetY: number
  isNormalized: boolean
}
