import React from 'react'

function Card(props) {
  const { title, url, content } = props.item
  return (
    <div className="about-item">
      <a href={url} target="_blank" rel="noopener noreferrer">
        <p className="about-item-title">{title}</p>
        {url && <p className="about-item-content">{url}</p>}
        {content && <p className="about-item-content">{content}</p>}
      </a>
    </div>
  )
}

export default Card
