import { bgm, stage, scene, dialog, choice, flow, util } from '../../api'
import { color } from '../../../config'
import { initRoadSignData } from '../../utils'
import { townStreet } from '../../common'

const option11 = [
  dialog.show(`别急着往海边跑嘛，先把行李放到民宿去吧！你预定的民宿叫做[style={"fill": "${color.tomato}"}]彩虹天堂[/style]唷！`),
  flow.goto(data => {
    return {
      node: data.roadSign.index
    }
  })
]
const option12 = [
  dialog.show(`先把行李放在民宿里，再来逛街会比较轻松吧！？还是快点前往[style={"fill": "${color.tomato}"}]彩虹天堂[/style]吧！`),
  flow.goto(data => {
    return {
      node: data.roadSign.index
    }
  })
]
const option21 = [
  dialog.show(`[style={"fill": "${color.tomato}"}]彩虹天堂[/style]民宿的方向不是往这里唷，请重新选择吧！`),
  flow.goto(data => {
    return {
      node: data.roadSign.index
    }
  })
]
const option22 = [
  dialog.show(`你该不会想再遇到刚才那个恶毒的女人吧？请重新选择前往[style={"fill": "${color.tomato}"}]彩虹天堂[/style]民宿的方向吧！`),
  flow.goto(data => {
    return {
      node: data.roadSign.index
    }
  })
]
const option23 = [
  dialog.show(`从这里过去就可以前往[style={"fill": "${color.tomato}"}]彩虹天堂[/style]民宿了吧！出发前是否要先存个档呢？`),
  bgm.stop('shopping'),
  stage.curtain.down(500),
  flow.goto('chapter1_4'),
]

const option1 = [
  choice.select(
    [
      '海边',
      '商店街',
      '取消'
    ],
    [
      (data, index) => {
        data.roadSign.seaside++
        return {
          node: index + 1
        }
      },
      (data, index) => {
        data.roadSign.shopping++
        return {
          node: index + option11.length + 1
        }
      },
      data => {
        return {
          node: data.roadSign.index
        }
      }
    ]
  ),

  ...option11,
  ...option12
]

const option2 = [
  choice.select(
    [
      '海神寺',
      '帆船公园',
      '彩虹天堂',
      '取消'
    ],
    [
      (data, index) => {
        data.roadSign.temple++
        return {
          node: index + 1
        }
      },
      (data, index) => {
        data.roadSign.park++
        return {
          node: index + 1 + option21.length
        }
      },
      (data, index) => {
        data.roadSign.hostel++
        return {
          node: index + 1 + option21.length + option21.length
        }
      },
      data => {
        return {
          node: data.roadSign.index
        }
      }
    ]
  ),

  ...option21,
  ...option22,
  ...option23
]

export const chapter1_3 = [
  stage.curtain.down(),
  scene.show('voiceover', 'voiceover'),
  stage.curtain.up(500),

  stage.voiceover.show(
    util.colorText('海风镇大街', color.deepskyblue)
  ),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.hide('voiceover'),
  ...townStreet,
  stage.curtain.up(500),

  bgm.play('shopping', {
    loop: true
  }),

  dialog.show(
    [
      '啊……这、这里就是海风镇的大街吗……？看起来很不错啊！',
      '唔……都是那个恶毒的女人……害我要用双脚外八的姿势走路……真是痛死我了……'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '双腿间不断传来的痛楚，让我每走一步路就疼痛难忍，好不容易来到大街上，总算让我送了口气。',
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      `嗯……记得民宿的人说，只要看到路牌就可以找到往[style={"fill": "${color.tomato}"}]帆船公园[/style]的方向，穿过[style={"fill": "${color.tomato}"}]帆船公园[/style]就会到达民宿了吧？`,
      `哦~这里有个路牌啊，看来只要[style={"fill": "${color.tomato}"}]确认路牌[/style]就会出现想要前往的地方吧。`,
      '真是太好了，我得快点前往民宿才行。',
      '对了！为了避免种种因素会让我被那个恶毒女人再踹一次，前往民宿之前先存档一下会比较好吧！'
    ],
    '以翔'
  ),

  flow.data((data, index) => {
    if (data.roadSign.chapter !== 'chapter1_3') {
      data.roadSign = initRoadSignData('chapter1_3', index + 1)
    }
  }),

  dialog.show(
    `事不宜迟，现在就前往[style={"fill": "${color.tomato}"}]彩虹天堂[/style]民宿吧！`,
    '以翔',
    true
  ),

  choice.button([
    {
      id: 'left',
      resource: 'map01010a-0',
      options: {
        x: 32,
        y: 134,
        action: data => {
          data.roadSign.target = 1
        }
      }
    },
    {
      id: 'right',
      resource: 'map01010a-1',
      options: {
        x: 253,
        y: 168,
        action: data => {
          data.roadSign.target = 2
        }
      }
    }
  ]),

  dialog.show('接下来想前往什么地方呢？'),
  flow.goto((data, index) => {
    const roadSign = data.roadSign

    if (roadSign.target === 1) {
      return {
        node: index + 1
      }
    } else {
      return {
        node: index + 1 + option1.length
      }
    }
  }),

  ...option1,
  ...option2
]
