import {
  SpriteComponent,
  AnimationComponent,
  TransformComponent,
  TransparentComponent,
  FadeComponent
} from '../../../../engine/components'
import { CharacterComponent } from '../../../components/tag'
import { GroupMap } from '../util'

export const group = new GroupMap('character')

export function createComponent(world, component) {
  const entity = world.createEntity()
    .addComponent(CharacterComponent)
    .addComponent(TransformComponent, {
      x: component.x,
      y: component.y
    })
    .addComponent(TransparentComponent, {
      alpha: 1
    })

  if (Array.isArray(component.res)) {
    entity.addComponent(AnimationComponent, {
      layer: `UserLayer${component.layer + 2}`,
      texturesId: component.res,
      cycle: 5000,
      animationSpeed: 0.2
    })
  } else {
    entity.addComponent(SpriteComponent, {
      layer: `UserLayer${component.layer + 2}`,
      textureId: component.res
    })
  }

  return entity
}

/**
 * 添加过渡
 * 
 * TODO: 当两个图层部分重叠的时候，重叠部分会有明显的差异，
 * 这问题弄起来太麻烦，就先凑合着用吧
 * 
 * @param {Entity} entity 
 * @param {number} alpha 
 * @param {number} duration 
 * @returns {Entity}
 */
export function addFade(entity, alpha, duration = 0) {
  const Transparent = entity.getComponent(TransparentComponent)
  if (duration > 0) {
    Transparent.alpha = 1 - alpha
    entity.addComponent(FadeComponent, {
      targetId: 'TransparentComponent',
      start: {
        alpha: 1 - alpha
      },
      end: {
        alpha
      },
      duration
    })
  } else {
    Transparent.alpha = alpha
  }

  return entity
}

/**
 * 判断过渡是否完成
 * @param {Entity} entity 实体
 * @returns {boolean}
 */
export function fadeCompleted(entity) {
  return !entity.hasComponent(FadeComponent)
}
