import { group, addFade, fadeCompleted } from './util'

export function hide(id) {
  function init(world) {
    const entityIdList = group.get(world, id)
  
    if (typeof entityIdList !== 'undefined') {
      entityIdList.forEach(item => {
        const entity = world.getEntity(item)
        addFade(entity, 0)
      })
    }
  }

  function process(world) {
    const entityIdList = group.get(world, id)
    let flag = true

    if (typeof entityIdList !== 'undefined') {
      entityIdList.forEach(item => {
        const entity = world.getEntity(item)
        if (fadeCompleted(entity)) {
          group.delete(world, id)
          world.removeEntity(entity)
        } else {
          flag = false
        }
      })
    }

    return flag
  }

  return {
    init,
    process
  }
}
