import * as PIXI from 'pixi.js'
import { Signal } from 'signals'
import FlowerFlyBackground from './flower'
import { gameInfo, startMenuStyle } from '../../config'
import { Sound } from '../../../engine/sound'

const fontStyle = startMenuStyle

const btnDownStyle = {
  fill: '#ffffff',
  stroke: '#F9B541'
}

export class Title {
  static onStartClicked = new Signal()
  static onLoadClicked = new Signal()
  static onSettingClicked = new Signal()
  static onAboutClicked = new Signal()

  constructor() {
    this.view = new PIXI.Container()
    this.init()
  }

  init() {
    this._bgmPlay()
    this._addBackground()
    this._addPtcLayer()
    this._addMenu()
  }

  _addBackground() {
    const background = PIXI.Sprite.from('title_background')
    this.view.addChild(background)
  }

  _addPtcLayer() {
    this.ptcLayer = new PIXI.Container()
    this.view.addChild(this.ptcLayer)
    this.flowerFly = []
    for (let i = 0; i < 5; i++) {
      let amount = 5
      if (i === 2) {
        amount = 2
      }
      this.flowerFly.push(new FlowerFlyBackground({
        width: gameInfo.width,
        height: gameInfo.height,
        amount,
        maxDist: 100,
        parent: this.ptcLayer,
        textureId: i
      }))
    }
  }

  _addMenu() {
    const group = new PIXI.Container()
    group.x = 365
    group.y = 380
    const start = this._createButton(0, '开始游戏', () => {
      this._bgmStop()
      Title.onStartClicked.dispatch()
    })
    const load = this._createButton(1, '继续游戏', () => {
      this._bgmStop()
      Title.onLoadClicked.dispatch()
    })
    const setup = this._createButton(2, '系统设置', () => {
      Title.onSettingClicked.dispatch()
    })
    const about = this._createButton(3, '关于游戏', () => {
      Title.onAboutClicked.dispatch()
    })
    group.addChild(start, load, setup, about)
    this.view.addChild(group)
  }

  _bgmPlay() {
    Sound.play('system_title', {
      loop: true
    })
  }

  _bgmStop() {
    Sound.stop('system_title')
  }

  _createButton(index, text, clickHandle) {
    const btnSpace = 30
    const btn = new PIXI.Text(text, fontStyle)
    btn.y = btnSpace * index
    btn.interactive = true
    btn.buttonMode = true
    // 解决文字模糊问题
    btn.resolution = 2
    btn.anchor.set(0.5)
    btn
      .on('pointerdown', onButtonDown)
      .on('pointerup', onButtonUp)
      .on('pointerupoutside', onButtonUp)
      .on('pointerover', onButtonOver)
      .on('pointerout', onButtonOut)
      .on('pointertap', (e) => {
        e.data.originalEvent.preventDefault()
        clickHandle()
      })
    return btn
  }

  update() {
    this.flowerFly.forEach(item => {
      item.tick()
    })
  }
}

function onButtonDown() {
  const btn = this
  // console.log('pointerdown')
  btn.isdown = true
  btn.scale.set(0.95)
}

function onButtonUp() {
  const btn = this
  if (btn.isOver) {
    btn.style.fill = btnDownStyle.fill
    btn.style.stroke = btnDownStyle.stroke
  } else {
    btn.style.fill = fontStyle.fill
    btn.style.stroke = fontStyle.stroke
  }
  if (!btn.isdown) {
    return
  }
  // console.log('pointerup')
  btn.isdown = false
  btn.scale.set(1)
}

function onButtonOver() {
  const btn = this
  btn.isOver = true
  // console.log('pointerover')
  if (btn.isdown) {
    return
  }
  btn.style.fill = btnDownStyle.fill
  btn.style.stroke = btnDownStyle.stroke
}

function onButtonOut() {
  const btn = this
  btn.isOver = false
  if (btn.isdown) {
    return
  }
  // console.log('pointerout')
  btn.style.fill = fontStyle.fill
  btn.style.stroke = fontStyle.stroke
}
