import { bgm, stage, scene, dialog, flow, util } from '../../api'
import { color } from '../../../config'

const commonStart = [
  stage.curtain.down(),
  scene.show('voiceover', 'voiceover'),
  stage.curtain.up(500),

  stage.voiceover.show(
    util.colorText('帆船公园', color.deepskyblue)
  ),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.hide('voiceover'),
  scene.show('park', 'map01008'),
  stage.curtain.up(500),

  bgm.play('shopping', {
    loop: true
  }),
]

const commonEnd = [
  bgm.stop('shopping'),
  stage.curtain.down(500),
  flow.goto('chapter1_5')
]

// 帆船公园
export const chapter1_5_4 = [
  ...commonStart,

  dialog.show(
    util.colorText(
      [
        '我随意逛着，不知不觉又来到帆船公园，此时的游客比中午艳阳高照时来得多。',
        '人们尽情的享受着公园里阴凉的树荫，几个调皮捣蛋的孩童围绕着跷跷板、秋千哇哇乱叫着。',
        '零散的小贩沿着绿穗步道向来往的游客兜售着各式各样的纪念品。',
        '我一边欣赏着绿穗步道的美景，一边拿着手机将眼前的画面一一记录下来。',
        '经过步道旁的树丛时，我忽然想起稍前刚到海风镇的时候，在这里遇到的奇怪女孩。'
      ],
      color.lightskyblue
    )
  ),

  stage.curtain.down(500),
  dialog.hide(),
  scene.hide('park'),
  scene.show('cg03001', 'map03001'),
  stage.curtain.up(500),

  dialog.show(
    [
      '真、真没想到会发生这种事情……现在回想起来还是觉得……',
      '唉……好歹对方也是一个长相可爱的女孩，就某种程度来说，也算时一种艳遇吧！'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '想到这里，不知道为什么，脚步竟然轻快了起来。',
      color.lightskyblue
    )
  ),
  dialog.show(
    '距离晚上还有一点时间，再到其他地点看看吧！',
    '以翔'
  ),

  ...commonEnd
]

