import { IComponentClass, ISystem } from '../core'
import { Entity } from '../core/entity'
import {
  SpriteComponent,
  AnimationComponent,
  TextComponent,
  TransformComponent,
  TransparentComponent
} from '../components'
import RenderComponent from '../components/base/render'
import { Scene } from '../render'

export class PIXIRenderSystem extends ISystem {
  public execute() {
    const entities = this.queries.entities.results

    for (const entity of entities) {
      this.match(entity, SpriteComponent)
      this.match(entity, AnimationComponent)
      this.match(entity, TextComponent)
    }
  }

  private match<T extends RenderComponent>(
    entity: Entity,
    componentClass: IComponentClass<T>
  ) {
    if (entity.hasComponent(componentClass)) {
      const sprite = entity.getComponent(componentClass)
      this.configureSprite(sprite)
      this.transform(entity, sprite)
      this.transparent(entity, sprite)
    }
  }

  private configureSprite(sprite: RenderComponent) {
    sprite.view.visible = sprite.visible
    const anchor = {
      x: sprite.anchor.x * sprite.view.width,
      y: sprite.anchor.y * sprite.view.height
    }
    sprite.view.pivot.set(anchor.x, anchor.y)
    if (!sprite.isRender) {
      const scene = this.world as Scene
      sprite.isRender = true
      scene.addSprite(sprite.layer, sprite.view)
    }
  }

  private transform(entity: Entity, sprite: RenderComponent) {
    if (entity.hasComponent(TransformComponent)) {
      const transform = entity.getComponent(TransformComponent)
      sprite.view.x = transform.x
      sprite.view.y = transform.y
      sprite.view.angle = transform.angle
      sprite.view.scale.set(transform.scale)
    }
  }

  private transparent(entity: Entity, sprite: RenderComponent) {
    if (entity.hasComponent(TransparentComponent)) {
      const transparent = entity.getComponent(TransparentComponent)
      sprite.view.alpha = transparent.alpha
    }
  }
}

PIXIRenderSystem.queries = {
  entities: {
    type: 'any',
    components: [SpriteComponent, AnimationComponent, TextComponent]
  }
}
