import { ISystem } from '../core'
import { TextComponent, TypewriterComponent } from '../components'

export class TypewriterSystem extends ISystem {
  public execute(delta: number) {
    const entities = this.queries.entities.results

    for (const entity of entities) {
      const Text = entity.getComponent(TextComponent)
      const typewriter = entity.getComponent(TypewriterComponent)
      if (typewriter.text.length > Text.text.length) {
        if (typewriter.speed <= 0) {
          typewriter.speed = 10
        }
        typewriter.deltaTimer += delta
        let time = 1000 / typewriter.speed
        if (Text.text.length === 0 && typewriter.printDelay >= 0) {
          // 打印延时，不至于显示的太快
          time = typewriter.printDelay
        }
        if (typewriter.deltaTimer >= time) {
          let char
          do {
            char = typewriter.text.charAt(Text.text.length)
            // 打印时处理下 bbcode 要不然会把标签也打印出来
            const result = processBbcode(typewriter.text, char, Text.text.length, 7)
            if (result.type === 'tag') {
              Text.text +=  result.bbcode
              char = typewriter.text.charAt(Text.text.length)
            }
            Text.text += char
            // 干掉空格，要不然会出现不自然的等待时间
          } while (char === ' ')
          // Text.text += typewriter.text.charAt(Text.text.length)
          typewriter.deltaTimer = 0
        }
      } else {
        entity.removeComponent(TypewriterComponent)
      }
    }
  }
}

TypewriterSystem.queries = {
  entities: [TextComponent, TypewriterComponent]
}

function processBbcode(string: string, char: string, i: number, magic: number) {
  let type = 'none'
  let bbcode = ''
  if (char === '[') {
    let index = i
    index += magic // magic should be 7
    bbcode = string.slice(i, index + 1)
    if (bbcode === '[style={') {
      let tmp
      do {
        index++
        tmp = string.charAt(index)
        bbcode += tmp
      } while (tmp !== ']')
      type = 'tag'
    } else if (bbcode === '[/style]') {
      type = 'tag'
    }
  }

  return {
    type,
    bbcode
  }
}
