import * as PIXI from 'pixi.js'
import { IComponent } from '../../core'

type Anchor = {
  x: number
  y: number
}

export default class RenderComponent extends IComponent {
  // serialize fields
  public layer: string
  /** 锚点（百分比） */
  public anchor: Anchor
  public visible: boolean
  // temp fields
  public view: PIXI.Container
  public isRender: boolean

  constructor() {
    super()
    this.layer = 'UserLayer0'
    this.anchor = {
      x: 0,
      y: 0
    }
    this.visible = true

    this.view = null
    this.isRender = false
  }

  public revert() {
    this.layer = 'UserLayer0'
    this.anchor = {
      x: 0,
      y: 0
    }
    this.visible = true

    if (this.view) {
      this.view.destroy()
      this.view = null
    }
    this.isRender = false
  }

  public getRenderJSON() {
    const { layer, anchor, visible } = this
    return { layer, anchor, visible }
  }
}
