import { bgm, stage, scene, dialog, flow, util } from '../../api'
import { color } from '../../../config'

const commonStart = [
  stage.curtain.down(),
  scene.show('voiceover', 'voiceover'),
  stage.curtain.up(500),

  stage.voiceover.show(
    util.colorText('海边', color.deepskyblue)
  ),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.hide('voiceover'),
  scene.show('seaside', 'map01001'),
  stage.curtain.up(500),

  bgm.play('shopping', {
    loop: true
  }),
]

const commonEnd = [
  bgm.stop('shopping'),
  stage.curtain.down(500),
  flow.goto('chapter2_2')
]

// 海边
export const chapter2_2_1 = [
  ...commonStart,

  dialog.show(
    '哇~好清爽啊！',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      [
        '我踩在柔软的沙滩上，伸了个大大的懒腰。',
        '远处已经有一些零星的船只正在作业着，看着不断拍打着沙滩的浪花，我忍不住脱下鞋，将脚浸在透明的海水中。'
      ],
      color.lightskyblue
    )
  ),
  dialog.show(
    '呜哇！好棒啊！冰冰凉凉的！',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '看着随海水退去，从我脚边流失的沙子，有几颗小小的贝壳顺着水流的力量滚动着。',
      color.lightskyblue
    )
  ),
  dialog.show(
    '这里……很适合带着绫音一起来散步啊……',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '我弯腰捡起一个纯白的贝壳，上头因为被海水冲刷，呈现流线圆润的美丽造型。',
      color.lightskyblue
    )
  ),
  dialog.show(
    '绫音小姐，请问你愿意收下这个代表我心意的美丽贝壳吗？',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '我假象着绫音就在我身边，举起手对着空气弯了弯腰，伸出的掌心上头，纯白色的贝壳隐隐闪动着光芒。',
      color.lightskyblue
    )
  ),
  dialog.show(
    '以、以翔！你对我真好……',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '我模仿绫音温柔的嗓音，将贝壳收回，用双手十分珍惜地捧在胸前，害羞地对自己说道。',
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '哈哈！感觉真不错，如果这时候再放点烟火来点缀的话，就更棒了啊！说不定接下来就会……',
      '啊，超期待的呀……！！'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '我高兴地想着，完全没注意到旁边的游客已经用异样的眼光看着我……。',
      color.lightskyblue
    )
  ),
  dialog.show(
    '为了好好安排行程，再到其他景点逛逛吧！',
    '以翔'
  ),

  ...commonEnd
]
