import { IComponentClass, IComponent } from './component'

/**
 * 组合查询关键字
 * @param componentClassList 要查询的类数组
 */
export function queryKey(componentClassList: IComponentClass<IComponent>[]) {
  return componentClassList.map(componentClass => componentClass._id).join('-')
}

/** uuid 生成器 */
export function generateUUID() {
  let d = new Date().getTime()
  let uuid = 'xxxxxxxx-xxxx-xxxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    let r = (d + Math.random() * 16) % 16 | 0
    d = Math.floor(d / 16)
    return (c === 'x' ? r : (r & 0x3) | 0x8).toString(16)
  })
  return uuid
}

// Detector for browser's "window"
export const hasWindow = typeof window !== "undefined"

// performance.now() "polyfill"
export const now =
  hasWindow && typeof window.performance !== "undefined"
    ? performance.now.bind(performance)
    : Date.now.bind(Date)
