import {
  TextComponent,
  ButtonComponent,
  TransformComponent
} from '../../../../engine/components'
import { ChoiceComponent } from '../../../components/tag'
import { gameInfo, buttonStyle, textBackground } from '../../../config'
import { group } from './util'

function createButton(world, text, x, y) {
  const entity = world.createEntity()
    .addComponent(ChoiceComponent)
    .addComponent(ButtonComponent)
    .addComponent(TransformComponent, { x, y })
    .addComponent(TextComponent, {
      layer: 'UserLayer8',
      text,
      style: buttonStyle,
      bgTextureId: textBackground.button.id,
      padding: textBackground.button.padding,
      position: {
        x: 'center',
        y: 'center'
      }
    })
  
  return entity
}

export function select(options, actions) {
  let gotoResult = {}
  options = options || []
  actions = actions || []

  function init(world) {
    let entityIdList = group.get(world, 'select')

    if (entityIdList || options.length <= 0) {
      return
    }

    const x = (gameInfo.width - textBackground.button.width) / 2
    let y = (gameInfo.height - (textBackground.button.height * options.length + 15 * (options.length - 1))) / 2

    entityIdList = []
    options.forEach(item => {
      const entity = createButton(world, item, x, y)
      y = y + textBackground.button.height + 15
      entityIdList.push(entity.id)
    })
    group.set(world, 'select', entityIdList)
  }

  function process(world) {
    let entityIdList = group.get(world, 'select')

    if (typeof entityIdList === 'undefined') {
      return true
    }

    const count = entityIdList.length

    for (let i = 0; i < count; i++) {
      const entity = world.getEntity(entityIdList[i])
      const btn = entity.getComponent(ButtonComponent)
      if (btn.isClick === true) {
        const action = actions[i]
        gotoResult = {}
        if (typeof action === 'number') {
          gotoResult.node = action
        } else if (typeof action === 'function') {
          gotoResult = action(world.storyData, world.systemData.nodeIndex)
        } else {
          gotoResult = null
        }

        entityIdList.forEach(item => {
          const entity = world.getEntity(item)
          world.removeEntity(entity)
        })
        group.delete(world, 'select')
        return true
      }
    }
    return false
  }

  function goto() {
    return gotoResult
  }

  return {
    init,
    process,
    goto
  }
}