import * as PIXI from 'pixi.js'

export class Stage extends PIXI.Container {
  public numLayer: number = 0
  public layerMap: Map<string, PIXI.Container | PIXI.ParticleContainer> = new Map()

  /**
   * 添加图层到 Stage 中
   * @param layer 
   * @param id 
   * @param index 
   * @private
   */
  private _addLayer(
    layer: PIXI.Container | PIXI.ParticleContainer,
    id: string,
    index?: number
  ) {
    if (index) {
      this.addChildAt(layer, index)
    } else {
      this.addChild(layer)
    }
    this.layerMap.set(id, layer)
    this.numLayer++
  }

  /**
   * 创建图层
   * @param id 
   * @param index 
   */
  public createLayer(id: string, index?: number) {
    const layer = new PIXI.Container()
    this._addLayer(layer, id, index)
  }

  /**
   * 创建粒子图层
   * @param id 
   * @param index 
   */
  public createParticleLayer(id: string, index?: number) {
    const layer = new PIXI.ParticleContainer()
    this._addLayer(layer, id, index)
  }

  /**
   * 判断图层是否存在
   * @param id 
   */
  public hasLayer(id: string) {
    return this.layerMap.has(id)
  }

  /**
   * 获得图层
   * @param id 
   */
  public getLayer(id: string) {
    if (!this.layerMap.has(id)) {
      console.warn(`There is no layer whose id is "${id}".`)
    }
    return this.layerMap.get(id)
  }

  /**
   * 移除图层
   * @param id 
   */
  public removeLayer(id: string) {
    if (this.layerMap.has(id)) {
      const layer = this.layerMap.get(id)
      this.removeChild(layer)
      this.numLayer--
    } else {
      console.warn(`There is no layer whose id is "${id}".`)
    }
  }

  /**清理图层 */
  public clearLayer() {
    this.layerMap.forEach(layer => {
      layer.removeChildren(0)
    })
  }
}