import { Sprite, Texture } from 'pixi.js'
import RenderComponent from './base/render'
import { componentRegister } from '../core'

export class SpriteComponent extends RenderComponent {
  // serialize fields
  public textureId: string
  // temp fields
  public texture: Texture

  constructor() {
    super()
    this.textureId = null
    this.texture = null
  }

  public init() {
    if (this.textureId) {
      this.texture = Texture.from(this.textureId)
    } else {
      this.texture = Texture.EMPTY
    }
    this.view = new Sprite(this.texture)
  }

  public clear() {
    this.revert()
    this.textureId = null
    this.texture = null
  }

  public toJSON() {
    const { textureId } = this
    const render = this.getRenderJSON()

    return {
      textureId,
      ...render
    }
  }
}

componentRegister('SpriteComponent')(SpriteComponent)
