import { bgm, stage, choice, flow } from '../../api'

const common = [
  bgm.stop('shopping'),
  stage.curtain.down(500),
]

const temple = [
  ...common,

  flow.goto(data => {
    if (data.roadSign.temple > 1) {
      return {
        chapter: 'chapter1_5_3end'
      }
    } else {
      return {
        chapter: 'chapter1_5_3'
      }
    }
  })
]

const park = [
  ...common,

  flow.goto('chapter1_5_4')
]

const hostel = [
  ...common,
  flow.goto(data => {
    if (data.roadSign.temple > 0) {
      // 如果去海神寺触发过关键剧情了，就推进新剧情
      return {
        chapter: 'chapter1_6'
      }
    } else {
      return {
        chapter: 'chapter1_5_1'
      }
    }
  })
]

// 海风西街
export const west = [
  choice.select(
    [
      '海神寺',
      '帆船公园',
      '彩虹天堂',
      '取消'
    ],
    [
      (data, index) => {
        data.roadSign.temple++
        return {
          node: index + 1
        }
      },
      (data, index) => {
        data.roadSign.park++
        return {
          node: index + 1 + temple.length
        }
      },
      (data, index) => {
        data.roadSign.hostel++
        return {
          node: index + 1 + temple.length + park.length
        }
      },
      data => {
        return {
          node: data.roadSign.index
        }
      }
    ]
  ),

  ...temple,
  ...park,
  ...hostel
]
