import { bgm, stage, scene, dialog, character, choice, flow, util } from '../../api'
import { color } from '../../../config'
import { townStreet } from '../../common'
import { initRoadSignData } from '../../utils'
import { east } from './east'
import { west } from './west'

const firstEnter = [
  character.show('liu_man_2'),

  dialog.show(
    '给老子站住！',
    '刘蛮'
  ),
  dialog.show(
    '可恶！得快点摆脱他！接下来该往哪里跑才好啊！？',
    '以翔'
  ),

  character.hide('liu_man_2'),
  dialog.hide(),
]

export const chapter2_4 = [
  stage.curtain.down(),
  scene.show('voiceover', 'voiceover'),
  stage.curtain.up(500),

  stage.voiceover.show(
    util.colorText('海风镇大街', color.deepskyblue)
  ),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.hide('voiceover'),
  ...townStreet,
  stage.curtain.up(500),

  bgm.play('nerves', {
    loop: true
  }),

  flow.goto((data, index) => {
    if (data.roadSign.chapter === 'chapter2_4') {
      return {
        node: index + 1 + firstEnter.length
      }
    } else {
      return {
        node: index + 1
      }
    }
  }),

  ...firstEnter,

  flow.data((data, index) => {
    if (data.roadSign.chapter !== 'chapter2_4') {
      data.roadSign = initRoadSignData('chapter2_4', index + 1)
    }
  }),

  dialog.hide(),
  choice.button([
    {
      id: 'left',
      resource: 'map01010a-0',
      options: {
        x: 32,
        y: 134,
        action: data => {
          data.roadSign.target = 1
        }
      }
    },
    {
      id: 'right',
      resource: 'map01010a-1',
      options: {
        x: 253,
        y: 168,
        action: data => {
          data.roadSign.target = 2
        }
      }
    }
  ]),

  dialog.show('接下来想前往什么地方呢？'),
  flow.goto((data, index) => {
    const roadSign = data.roadSign

    if (roadSign.target === 1) {
      return {
        node: index + 1
      }
    } else {
      return {
        node: index + 1 + east.length
      }
    }
  }),

  ...east,
  ...west
]