import { ISystem } from '../../engine/core'
import { InputComponent } from '../components/singleton'

export class InputSystem extends ISystem {
  execute(delta) {
    const singleton = this.world.singleton
    const input = singleton.getComponent(InputComponent)

    if (this.world.interaction) {
      const interaction = this.world.interaction.mouse
      input.preEvent = {
        ...input.curEvent
      }

      // 更新鼠标位置
      input.curEvent.x = interaction.global.x
      input.curEvent.y = interaction.global.y

      // 更新按键信息
      input.curEvent.buttons = interaction.buttons

      // 更新鼠标按键状态
      if (interaction.isDown) {
        if (input.curEvent.isDown === false) {
          input.downPoint.x = input.curEvent.x
          input.downPoint.y = input.curEvent.y
        }
        input.curEvent.isDown = true
      } else {
        input.curEvent.isDown = false
      }

      if (
        input.preEvent.isDown === true &&
        input.preEvent.buttons === 1 &&
        input.curEvent.isDown === false &&
        Math.abs(input.downPoint.x - input.curEvent.x) <= 50 &&
        Math.abs(input.downPoint.y - input.curEvent.y) <= 50
      ) {
        input.readyClick = true
        input.deltaTimer = 0
      }

      input.isClick = false
      if (input.readyClick === true) {
        if (input.deltaTimer >= 10) {
          input.isClick = true
          input.readyClick = false
          input.deltaTimer = 0
        } else {
          input.deltaTimer += delta
        }
      }
    }
  }
}
