import * as PIXI from 'pixi.js'
import 'pixi-sound'
import { Signal } from 'signals'

export default class Loader {
  public source: PIXI.Loader
  public loaded: boolean
  public assets: any[] | undefined
  public onProgressed: Signal
  public onCompleted: Signal

  constructor(baseUrl?: string, concurrency?: number) {
    this.source = new PIXI.Loader(baseUrl, concurrency)
    this.loaded = false
    this.onProgressed = new Signal()
    this.onCompleted = new Signal()
  }

  public load(assets: any[]): Promise<any> {
    if (this.loaded === true) {
      return Promise.resolve(this.source.resources)
    }

    this.assets = assets
    this.source
      .add(assets)
      .on('progress', this._onProgress.bind(this))
      .on('complete', this._onComplete.bind(this))

    return new Promise((resolve, reject) => {
      this.source
        .on('error', this._onError(reject))
        .load(this._onLoad(resolve))
    })
  }

  private _onProgress(loader: PIXI.Loader, resource: PIXI.LoaderResource) {
    this.onProgressed.dispatch(loader.progress, resource)
  }

  private _onComplete(loader: PIXI.Loader, resources: PIXI.IResourceDictionary) {
    this.onCompleted.dispatch(loader.progress, resources)
  }

  private _onError(reject: (...args: any[]) => void) {
    return (error: Error) => {
      this.source.reset()
      reject(error)
    }
  }

  private _onLoad(resolve: (...args: any[]) => void) {
    return (loader: PIXI.Loader, resources: PIXI.IResourceDictionary) => {
      this.loaded = true
      resolve(resources)
    }
  }
}
