import { bgm, stage, dialog, choice, flow } from '../../api'

const seaside = [
  bgm.stop('shopping'),
  stage.curtain.down(500),
  flow.goto('chapter2_2_1')
]

const shopping = [
  dialog.show(
    '记得Sawa说过商店街还在筹备中，看来还是先去别的地方吧！',
    '以翔'
  ),
  flow.goto(data => {
    return {
      node: data.roadSign.index
    }
  })
]

// 海风东街
export const east = [
  choice.select(
    [
      '海边',
      '商店街',
      '取消'
    ],
    [
      (data, index) => {
        data.roadSign.seaside++
        return {
          node: index + 1
        }
      },
      (data, index) => {
        data.roadSign.shopping++
        return {
          node: index + 1 + seaside.length
        }
      },
      data => {
        return {
          node: data.roadSign.index
        }
      }
    ]
  ),

  ...seaside,
  ...shopping
]
