import { ISystem } from '../../engine/core'
import { DisplacementComponent, FadeComponent, SoundComponent } from '../../engine/components'
import { BGMComponent, RemoveComponent } from '../components/tag'

export class RemoveSystem extends ISystem {
  execute() {
    const removed = this.queries.removed.results

    for (const entity of removed) {
      if (entity.hasAnyComponent([DisplacementComponent, FadeComponent])) {
        continue
      }
      this.world.removeEntity(entity)
    }

    const bgm = this.queries.bgm.results

    for (const entity of bgm) {
      if (!entity.hasComponent(SoundComponent)) {
        this.world.removeEntity(entity)
      }
    }
  }
}

RemoveSystem.queries = {
  removed: [RemoveComponent],
  bgm: [BGMComponent]
}
