import { bgm, stage, dialog, choice, flow } from '../../api'

const seaside = [
  bgm.stop('shopping'),
  stage.curtain.down(500),
  flow.goto(data => {
    if (data.roadSign.seaside > 1) {
      return {
        chapter: 'chapter1_5_2end'
      }
    } else {
      return {
        chapter: 'chapter1_5_2'
      }
    }
  })
]

const shopping = [
  dialog.show('「本商店街为配合音浪祭典，所有店家重整中，如有不便之处，请多多包涵。」'),
  dialog.show(
    '商店街今天没开放啊……',
    '以翔'
  ),
  flow.goto(data => {
    return {
      node: data.roadSign.index
    }
  })
]

// 海风东街
export const east = [
  choice.select(
    [
      '海边',
      '商店街',
      '取消'
    ],
    [
      (data, index) => {
        data.roadSign.seaside++
        return {
          node: index + 1
        }
      },
      (data, index) => {
        data.roadSign.shopping++
        return {
          node: index + 1 + seaside.length
        }
      },
      data => {
        return {
          node: data.roadSign.index
        }
      }
    ]
  ),

  ...seaside,
  ...shopping
]
