import {
  TextComponent,
  TransformComponent,
  TypewriterComponent
} from '../../../../engine/components'
import { InputComponent } from '../../../components/singleton'
import { DialogComponent, NameComponent } from '../../../components/tag'
import { gameInfo, nameStyle, dialogStyle, textBackground } from '../../../config'
import { group } from './util'

const typewriterOptions = {
  speed: 22
}

function createDialog(world, text) {
  const entityId = group.get(world, 'text')
  if (entityId) {
    changeDialog(world.getEntity(entityId), text)
    return
  }

  const entity = world.createEntity('dialog')
    .addComponent(DialogComponent)
    .addComponent(TextComponent, {
      layer: 'UserLayer8',
      style: dialogStyle,
      bgTextureId: textBackground.dialog.id,
      padding: textBackground.dialog.padding
    })
    .addComponent(TransformComponent, {
      x: 0,
      y: gameInfo.height - textBackground.dialog.height
    })
    .addComponent(TypewriterComponent, { text, ...typewriterOptions })

  group.set(world, 'text', entity.id)
}

function changeDialog(entity, text) {
  const Text = entity.getComponent(TextComponent)
  Text.text = ''

  if (entity.hasComponent(TypewriterComponent)) {
    const Typewriter = entity.getComponent(TypewriterComponent)
    Typewriter.text = text
  } else {
    entity.addComponent(TypewriterComponent, { text, ...typewriterOptions })
  }
}

function createName(world, text) {
  const entityId = group.get(world, 'name')
  if (entityId) {
    const Text = world.getEntity(entityId).getComponent(TextComponent)
    Text.text = text
    return
  }

  const entity = world.createEntity('name')
    .addComponent(TextComponent, {
      layer: 'UserLayer8',
      text,
      style: nameStyle,
      bgTextureId: textBackground.name.id,
      padding: textBackground.name.padding,
      position: {
        x: 'center',
        y: 'center'
      }
    })
    .addComponent(TransformComponent, {
      x: 0,
      y: gameInfo.height - textBackground.name.height - textBackground.dialog.height
    })
    .addComponent(NameComponent)

  group.set(world, 'name', entity.id)
}

function removeName(world) {
  const entityId = group.get(world, 'name')
  if (entityId) {
    const name = world.getEntity(entityId)
    world.removeEntity(name)
    group.delete(world, 'name')
  }
}

export function show(texts, name, auto = false) {
  function init(world) {
    let text

    if (Array.isArray(texts)) {
      world.systemData.subIndex = 0
      text = texts[0]
      world.systemData.subIndex++
    } else {
      text = texts
    }
    createDialog(world, text)

    if (name) {
      createName(world, name)
    } else {
      removeName(world)
    }
  }

  function process(world) {
    const dialog = world.getEntity(group.get(world, 'text'))
    const input = world.singleton.getComponent(InputComponent)
    const isClick = auto ? true : input.isClick

    if (dialog.hasComponent(TypewriterComponent)) {
      if (input.isClick) {
        const text = dialog.getComponent(TypewriterComponent).text
        dialog.getComponent(TextComponent).text = text
        dialog.removeComponent(TypewriterComponent)
      }
      return false
    }

    if (isClick === false) {
      return false
    }

    if (
      Array.isArray(texts) &&
      world.systemData.subIndex < texts.length
    ) {
      const text = texts[world.systemData.subIndex]
      world.systemData.subIndex++
      changeDialog(dialog, text)
      return false
    } else {
      return true
    }
  }

  return {
    init,
    process
  }
}
