import { IComponent, componentRegister } from '../core'

export class TransparentComponent extends IComponent {
  public alpha : number

  constructor() {
    super()
    this.alpha = 0
  }

  clear() {
    this.alpha = 0
  }

  toJSON() {
    const { alpha } = this
    return { alpha }
  }
}

componentRegister('TransparentComponent')(TransparentComponent)
