import { InputComponent } from '../../../components/singleton'

function wait(delay) {
  let timer = 0
  function process(world, delta) {
    if (timer >= delay) {
      timer = 0
      return true
    } else {
      timer += delta
      return false
    }
  }

  return { process }
}

function waitClick() {
  function process(world) {
    const input = world.singleton.getComponent(InputComponent)

    if (input.isClick === false) {
      return false
    } else {
      return true
    }
  }

  return {
    process
  }
}

/**
 * 跳转，如果第一个参数是函数，第二个参数则不会被使用
 * @param {string | Function} chapter 
 * @param {number} node 
 */
function goto(chapter, node = 0) {
  function process() {
    return true
  }

  function goto(world) {
    if (typeof chapter === 'function') {
      return chapter(world.storyData, world.systemData.nodeIndex)
    } else {
      return {
        chapter,
        node
      }
    }
  }

  return {
    process,
    goto
  }
}

function data(callback) {
  function init(world) {
    callback(world.storyData, world.systemData.nodeIndex)
  }

  function process() {
    return true
  }

  return {
    init,
    process
  }
}

export default { wait, waitClick, goto, data }