import {
  SpriteComponent,
  TransformComponent,
  TransparentComponent,
  DisplacementComponent,
  FadeComponent
} from '../../../../engine/components'
import { BackgroundComponent } from '../../../components/tag'
import { group, addTransform } from './util'

function configOption(options) {
  options = options || {}

  options.render = {
    alpha: 1,
    ...options.render
  }

  if (options.animation) {
    options.animation = {
      duration: 1000,
      ...options.animation
    }
  }

  return options
}

function createEntity(world, id, resource, options) {
  options = configOption(options)
  const entityId = group.get(world, id)
  if (entityId) {
    return
  }

  const { render, animation } = options
  const entity = world.createEntity(id)
    .addComponent(BackgroundComponent)
    .addComponent(SpriteComponent, {
      layer: 'UserLayer0',
      textureId: resource
    })
    .addComponent(TransformComponent, {
      ...render.position
    })
    .addComponent(TransparentComponent, {
      alpha: render.alpha
    })

  group.set(world, id, entity.id)

  if (typeof animation === 'undefined') {
    return
  }

  const { position, alpha, duration = 1000 } = animation

  if (position) {
    addTransform(
      entity,
      DisplacementComponent,
      render.position,
      position,
      duration
    )
  }

  if (typeof alpha === 'number') {
    addTransform(
      entity,
      FadeComponent,
      { alpha: render.alpha },
      { alpha },
      duration
    )
  }
}

export function show(id, resource, options) {
  function init(world) {
    createEntity(world, id, resource, options)
  }

  function process() {
    return true
  }

  return {
    init,
    process
  }
}

export function show_async(id, resource, options) {
  function init(world) {
    createEntity(world, id, resource, options)
  }

  function process(world) {
    const entityId = group.get(world, id)
    if (entityId) {
      const entity = world.getEntity(entityId)
      if (
        entity.hasComponent(DisplacementComponent) ||
        entity.hasComponent(FadeComponent)
      ) {
        return false
      } else {
        return true
      }
    } else {
      return true
    }
  }

  return {
    init,
    process
  }
}