import { bgm, stage, scene, dialog, flow, util } from '../../api'
import { color } from '../../../config'

export const chapter1_1 = [
  stage.voiceover.show([
    ...util.colorText(
      [
        '我始终认为，人生就像一幅复杂的拼图，每个人总有属于自己的回忆碎片。',
        '总是在不自觉的当下，一片片平凑着，直到看见事件的原始轮廓',
        '自从那次旅行，即使到现在，我的脑海里仍深深烙印着那年夏天的艳阳、海风，以及……',
        '你的微笑……'
      ],
      color.lightskyblue
    ),
    util.colorText('第一章    《 邂 逅 》', color.burlywood),
    util.colorText('海边', color.deepskyblue)
  ]),

  stage.curtain.down(500),
  stage.voiceover.hide(),
  scene.show('hai_bian_d', 'map01001'),
  stage.curtain.up(500),

  bgm.play('shopping', {
    loop: true
  }),

  dialog.show(
    [
      '哇！好刺眼的太阳啊！坐了那么久的车，终于到达海风镇啦！',
      '哦！是海耶！凉爽的海风……哇……心情都好了起来啊……',
      '真不愧是每年举办音浪祭的专属地点啊……',
      '……\n这么漂亮的景色……她看到也会很开心的吧……',
      '虽然我们约好要在这边会和……但是她……',
      '唉……有事耽搁了，也是没办法的事啊……'
    ],
    '？？？'
  ),
  dialog.show(
    util.colorText(
      [
        '我，以翔，一个平凡的大一生。',
        '努力打工了这么久，总算存到了这趟来海风镇的旅费……',
        '虽然很辛苦……',
        '但是，这是我必须为她做的……',
        '补偿吧……'
      ],
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '彩虹天堂民宿应该离这没多远吧……',
      '记得民宿的人说……下车后步行五百公尺，看到小吃店再左转三百公尺，穿过帆船公园之后再往右走一百公尺就到了……',
      '……\n还……还真不是普通的近哈……'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '阳光穿过云层，照射在阵雨过后还有些滋润的公路上。路上一大滩雨后的积水，正一点一滴因为热度而蒸发。',
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '看这里也不像有计程车经过的样子……',
      '好吧！只好走到民宿去了，顺便欣赏海风镇的海景也很不错啊！'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      [
        '我振奋精神，绕过脚边雨后残留的水坑，沿着路旁青翠的草地，迈开脚步准备往前走时……',
        '噗呲~'
      ],
      color.lightskyblue
    )
  ),

  scene.shake('hai_bian_d', scene.baseShake),

  dialog.show(
    util.colorText(
      [
        '一辆车子从我身旁呼啸而过，狠狠碾过我刻意避开的积水，我连忙往旁边的草地一跳，试图闪过溅起的水花。',
        '但是我毕竟不是练就飞檐走壁轻功的武林高手。溅起的水花飞快地袭击了我的下半身。'
      ],
      color.lightskyblue
    )
  ),
  dialog.show(
    '哇啊！这……这车子怎么搞的！？这可是我为了这次旅行新买的海滩裤啊……！！',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      [
        '我低头看着惨遭“海啸”洗礼的裤子，水还意犹未尽地在裤管边缘往下滴。',
        '而被水花溅透的地方，湿的有些巧妙……让我看起来活像个尿失禁的家伙……',
        '这时，脚下除了柔软的草地之外，还有另一种奇怪的触感……该……该不会……',
        '我紧张地缓缓抬起脚，忽然觉得脚踝沉重了起来……'
      ],
      color.lightskyblue
    )
  ),

  bgm.stop('shopping'),
  bgm.play('funny', {
    loop: true
  }),

  dialog.show(
    '呃啊！',
    '以翔'
  ),
  dialog.show(
    util.colorText(
      [
        '我抬起脚底，一坨热呼呼湿黏黏的动物排泄物，正对着我的海滩鞋死缠烂打地做亲密接触……',
        '其间还有金黄灿烂的玉米粒娇羞地瑟缩在我的鞋底缝隙间……',
        '我急忙就着人行道的台阶疯狂地蹭着鞋子……'
      ],
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '哇啊！气死我了！',
      '离民宿到底有多远啊……！？',
      '方圆百里居然看不到什么商家……'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '这……这是地狱吗……！？我该不会糊里糊涂搭上通往地狱入口的班车吧？！',
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '哎……想不到刚下车就遇到这些事情，真不知道后面还会有什么事在等着我……',
      '如果……如果……她在的话，想必会笑我像个笨蛋吧……'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '我的脑海中不自觉浮现她的笑脸，温柔的笑声仿佛就在我的耳边回荡着……',
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '……\n是啊，计划这次旅行的目的不就是为了她吗……？',
      '为了……弥补那道……裂痕……不是吗……？'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      '想到这里，我收回一直蹭在阶梯上的脚，假装忘记半颗缩在鞋底的玉米粒与双腿间湿黏的闷热感……',
      color.lightskyblue
    )
  ),
  dialog.show(
    [
      '看来，只好走一步算一步了。',
      '反正船到桥头自然直吧！'
    ],
    '以翔'
  ),
  dialog.show(
    util.colorText(
      [
        '揉揉鼻子，我再次拉紧背包，继续往前走去。',
        '暗暗期待可以让我换下这身狼狈的地方，会出现在一个转角……'
      ],
      color.lightskyblue
    )
  ),

  stage.curtain.down(500),
  flow.goto('chapter1_2')
]
