import React from 'react'
import './index.css'

class Loader extends React.Component {
  constructor() {
    super()
    this.state = {
      show: false
    }
  }

  detailsHandle = () => {
    this.setState({
      show: !this.state.show
    })
  }

  render() {
    const { loading, progress, error } = this.props.loader
    let style = 'loader'

    if (this.state.show) {
      style += ' open'
    }

    return (
      <div className="loader-wrapper" style={{display: loading ? 'block' : 'none'}}>
        <div className="loader-container">
          <div className={style}>
            <em style={{ width: `${progress}%` }}></em>
            <div className="details-panel">
              <p>
                <strong>Progress:</strong> <font>{`${progress}%`}</font>
              </p>
              {
                error && (
                  <p>
                    <strong>Error:</strong> <font style={{color: 'red'}}>{'resource loading failed.'}</font>
                  </p>
                )
              }
            </div>
          </div>
          <div className="details button" onClick={this.detailsHandle}>Details</div>
        </div>
      </div>
    )
  }
}

export default Loader
