import { Scene } from '../../../engine/render'
import {
  PIXIRenderSystem,
  AnimationSystem,
  TypewriterSystem,
  TextButtonSystem,
  TransformSystem,
  TextUpdateSystem,
  SoundSystem
} from '../../../engine/systems'
import {
  encryptByRabbit,
  decryptByRabbit,
  saveToLocalStorage,
  loadFromLocalStorage
} from '../../../engine/utils'
import {
  InputSystem,
  StorySystem,
  RemoveSystem
} from '../../systems'
import {
  InputComponent,
  // StoryComponent
} from '../../components/singleton'

import story from '../../story'

export class Main {
  constructor(game) {
    this.game = game
    this.world = null
    this.view = null

    this.init()
  }

  init() {
    this._initWorldModule()
    this._initData()
    this.world.start()
  }

  saveArchive() {
    const data = {
      engine: this.world.serialize(),
      system: this.world.systemData,
      story: this.world.storyData
    }

    if (process.env.NODE_ENV === 'development') {
      console.log('save data:', data)
    }

    saveToLocalStorage('data', encryptByRabbit(JSON.stringify(data)))
  }

  loadArchive() {
    let data = loadFromLocalStorage('data', true)

    if (data) {
      data = JSON.parse(decryptByRabbit(data))

      if (process.env.NODE_ENV === 'development') {
        console.log('load data:', data)
      }

      const { engine, system, story } = data
      this.world.deserialize(engine)
      this.world.systemData = system
      this.world.storyData = story
    }
  }

  _initWorldModule() {
    console.log(story)
    this.world = new Scene()
    this.world.interaction = this.game.renderer.plugins.interaction
    this.world.storyScript = story
    this.view = this.world.view
    window.world = this.world

    this.world
      .registerSystem(InputSystem)
      .registerSystem(StorySystem)
      .registerSystem(TypewriterSystem)
      .registerSystem(TextButtonSystem)
      .registerSystem(AnimationSystem)
      .registerSystem(TransformSystem)
      .registerSystem(TextUpdateSystem)
      .registerSystem(SoundSystem)
      .registerSystem(RemoveSystem)
      .registerSystem(PIXIRenderSystem)

    this.world.singleton
      .addComponent(InputComponent)
      // .addComponent(StoryComponent, {
      //   curChapter: 'chapter1',
      // })
  }

  _initData() {
    this.world.systemData = {
      entities: {},
      chapter: 'chapter1',
      initialed: false,
      nodeIndex: 0,
      subIndex: 0
    }
    this.world.storyData = {}
  }

  update(delta) {
    this.world.execute(delta)
  }

  stats(stats) {
    stats.entity.update(this.world.stats().overview.numEntities, 30)
  }
}
