import Stats from 'stats.js'
import { Signal } from 'signals'

import Loader from '../engine/loader'
import { Renderer } from '../engine/render'
import { InteractionManager } from '../engine/render/plugins'
import { Sound } from '../engine/sound'

import { gameInfo, assetRootUrl, assets } from './config'
import { CutsceneScene } from './scene'

Renderer.registerPlugin('interaction', InteractionManager)

class Game {
  constructor(title) {
    this.title = title

    this.scale = 1
    this.renderer = null

    this.loader = new Loader(assetRootUrl)

    this.cutscenes = new CutsceneScene(this)

    this.prevScene = null
    this.curScene = null
    this.show = null

    this.isPlay = true
    this.updated = new Signal()

    this.stats = new Stats()
    this.stats.added = false
    this.stats.entity = this.stats.addPanel( new Stats.Panel( 'ENTITY', '#ff8', '#221' ) )
    this.stats.showPanel(0) // 0: fps, 1: ms, 2: mb, 3+: custom

    this.init()
  }

  init() {
    if (process.env.NODE_ENV === 'development') {
      this.addStats()
    }
    this._initSoundModule()
    this._initRenderModule()
  }

  load() {
    return this.loader.load(assets)
  }

  addStats() {
    if (!this.stats.added) {
      document.body.appendChild(this.stats.dom)
      this.stats.added = true
    }
  }

  removeStats() {
    if (this.stats.added) {
      document.body.removeChild(this.stats.dom)
      this.stats.added = false
    }
  }

  _initSoundModule() {
    Sound.loadState()
  }

  _initRenderModule() {
    const resolution = window.devicePixelRatio || 1
    const width = gameInfo.width
    const height = gameInfo.height

    this.renderer = new Renderer({
      width,
      height,
      resolution
    })
    window.renderer = this.renderer.renderer
  }

  to(sceneClass, callback) {
    Sound.stopAll()
    const scene = new sceneClass(this)
    return this._cutscenesWait(scene, callback)
  }

  back(callback) {
    if (this.prevScene) {
      Sound.stopAll()
      return this._cutscenesWait(this.prevScene, callback)
    } else {
      return Promise.resolve()
    }
  }

  _cutscenesWait(scene, callback) {
    this.updated.addOnce(() => {
      this.show = this.cutscenes
    })
    const cutscenesWait = new Promise(resolve => {
      if (scene.load) {
        // 在这里可以做些类似加载的事
        scene.load().then(() => {
          callback && callback(scene)
          resolve()
        })
      } else {
        callback && callback(scene)
        // 否则就停一下然后切换场景
        setTimeout(resolve, 150)
      }
    })
    cutscenesWait.then(() => {
      this.updated.addOnce(() => {
        this._changeScene(scene)
      })
    })
    return cutscenesWait
  }

  start(sceneClass) {
    if (sceneClass) {
      const scene = new sceneClass(this)
      this._changeScene(scene)
    }
    requestAnimationFrame(this._update)
  }

  pause() {
    this.isPlay = false
  }

  resume() {
    this.isPlay = true
  }

  _changeScene(scene) {
    this.prevScene = this.curScene
    this.curScene = scene
    this.show = scene
    this._setSceneScale()
  }

  _update = delta => {
    this.stats.begin()
    if (this.show && this.isPlay) {
      this.show.update && this.show.update(delta)
      this.renderer.render(this.show.view)
    }
    this.updated.dispatch(this)
    this.stats.end()
    if (this.curScene.stats) {
      this.curScene.stats(this.stats)
    } else {
      this.stats.entity.update(0, 30)
    }
    requestAnimationFrame(this._update)
  }

  saveArchive() {
    return new Promise(resolve => {
      this.updated.addOnce(() => {
        if (this.curScene.saveArchive) {
          this.curScene.saveArchive()
        }
        resolve()
      })
    })
  }

  loadArchive() {
    return new Promise(resolve => {
      this.updated.addOnce(() => {
        if (this.curScene.loadArchive) {
          this.curScene.loadArchive()
        }
        resolve()
      })
    })
  }

  resize = () => {
    this._updateRenderScale()
    this._setSceneScale()
  }

  _updateRenderScale() {
    const windowWidth = window.innerWidth
    const windowHeight = window.innerHeight

    if (
      windowWidth < gameInfo.width ||
      windowHeight < gameInfo.height
    ) {
      this.scale = Math.min(
        windowWidth / gameInfo.width,
        windowHeight / gameInfo.height
      )
    }

    console.log('renderScale: ', this.scale)
    this.renderer.resize(
      gameInfo.width * this.scale,
      gameInfo.height * this.scale
    )
  }

  _setSceneScale() {
    if (this.show) {
      this.show.view.scale.set(this.scale)
    }
  }
}

const game = new Game('love-memory')
window.love_memory = game

export default game
