import { IComponent, componentRegister } from '../../../engine/core'

const BtnTypes = {
  letf: 0,
  center: 1,
  right: 2
}

export class InputComponent extends IComponent {
  constructor() {
    super()
    this.curEvent = {
      isDown: false,
      buttons: 0,
      x: -1,
      y: -1
    }
    this.preEvent = {
      isDown: false,
      buttons: 0,
      x: -1,
      y: -1
    }

    /** 按下时的坐标 */
    this.downPoint = { x: 0, y: 0 }
    /** 是否单击 */
    this.isClick = false
    /** 接收点击事件，准备改变isClick */
    this.readyClick = false
    /** 单击延时 */
    this.deltaTimer = 0
  }

  clear() {
    this.curEvent = {
      isDown: false,
      buttons: 0,
      x: -1,
      y: -1
    }
    this.preEvent = {
      isDown: false,
      buttons: 0,
      x: -1,
      y: -1
    }

    this.downPoint = { x: 0, y: 0 }
    this.isClick = false
    this.readyClick = false
    this.deltaTimer = 0
  }

  isMouseUp(btnType) {
    const { isDown, buttons: button } = this.curEvent

    if (isDown) {
      return false
    }
    if (btnType === BtnTypes.letf) {
      return button === 0
    } else if (btnType === BtnTypes.center) {
      return button === 1
    } else if (btnType === BtnTypes.right) {
      return button === 2
    } else {
      return false
    }
  }

  isMouseDown(btnType) {
    const { isDown, buttons: button } = this.curEvent

    if (!isDown) {
      return false
    }
    if (btnType === BtnTypes.letf) {
      return button === 0
    } else if (btnType === BtnTypes.center) {
      return button === 1
    } else if (btnType === BtnTypes.right) {
      return button === 2
    } else {
      return false
    }
  }
}

componentRegister('InputComponent')(InputComponent)
