import { IComponent, componentRegister } from '../core'

export class TransformComponent extends IComponent {
  public x: number
  public y: number
  public angle: number
  public scale: number

  constructor() {
    super()
    this.x = 0
    this.y = 0
    this.angle = 0
    this.scale = 1
  }

  clear() {
    this.x = 0
    this.y = 0
    this.angle = 0
    this.scale = 1
  }

  toJSON() {
    const { x, y, angle, scale } = this
    return { x, y, angle, scale }
  }
}

componentRegister('TransformComponent')(TransformComponent)
