import { GroupMap } from '../util'

export const group = new GroupMap('scene')

export function addTransform(entity, componentClass, start, end, duration) {
  if (entity.hasComponent(componentClass)) {
    entity.removeComponent(componentClass)
  }

  entity.addComponent(componentClass, {
    start,
    end,
    duration
  })
}


export const fadeIn = {
  render: {
    alpha: 0
  },
  animation: {
    duration: 1000,
    alpha: 1
  }
}

export const fadeOut = {
  render: {
    alpha: 1
  },
  animation: {
    duration: 1000,
    alpha: 0
  }
}

export const baseShake = {
  amplitude: 10,
  duration: 100
}
