export class GroupMap {
  constructor(group) {
    this.group = group
  }

  getGroup(world) {
    if (typeof world.systemData.entities[this.group] === 'undefined') {
      world.systemData.entities[this.group] = {}
    }
  
    return world.systemData.entities[this.group]
  }

  set(world, id, value) {
    this.getGroup(world)[id] = value
  }

  get(world, id) {
    return this.getGroup(world)[id]
  }

  delete(world, id) {
    this.set(world, id, undefined)
  }
}

export function colorText(texts, color) {
  if (Array.isArray(texts)) {
    return texts.map(text => (`[style={"fill": "${color}"}]${text}[/style]`))
  } else {
    return `[style={"fill": "${color}"}]${texts}[/style]`
  }
}
