import { IComponent, componentRegister } from '../core'

type Vars = {
  x?: number
  y?: number
  angle?: number
  scale?: number
}

/**
 * 位移组件
 */
export class DisplacementComponent extends IComponent {
  public start: Vars
  public end: Vars
  public duration: number

  constructor() {
    super()
    this.start = {}
    this.end = {}
    this.duration = 0
  }

  public clear() {
    this.start = {}
    this.end = {}
    this.duration = 0
  }

  public toJSON() {
    const { start, end, duration } = this
    return { start, end, duration }
  }
}

componentRegister('DisplacementComponent')(DisplacementComponent)
